export class Hotel {
    public id: number;
    public hotelName: string;
    public category: string;
    public telephone: string;
    public address: string;
    public location: string;
    public postalCode: string;
    public province: string;
    public country: any;
    public logo: any;
    public mainImage: any;
    public mainImageUrl:string;
    public receptionistName: string;
    public receptionistPosition: string;
    public receptionistImage: any;
    public receptionistText: string;
    public description: string;
    public lat: string;
    public lon: string;
    public imageInformation: any;
    public urlWeb: string;
    public userFacebook: string;
    public twitterUser: string;
    public defaultCheckinTime: string = '16: 00 ';
    public defaultCheckoutTime: string = '12: 00';
    public currency: any;
    public stripePublicKey: string;
    public healthService: boolean = false;
    public roomService: boolean = false;
    public offerService: boolean = false;
    public chatService: boolean = false;
    public activityService: boolean = false;
    public restaurantService: boolean = false;
    public checkinService: boolean = false;
    public checkoutService: boolean = false;
    public noStayHealthService: boolean = false;
    public noStayOfferService: boolean = false;
    public noStayChatService: boolean = false;
    public noStayActivityService: boolean = false;
    public noStayRestaurantService: boolean = false;

}



