import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, MenuController, AlertController, Loading, LoadingController } from 'ionic-angular';
import { BookingServiceProvider } from '../../providers/booking-service/booking-service';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { ToolsProvider } from '../../providers/tools/tools';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { Constant } from '../../constants/constants';
import { NotificationProvider } from '../../providers/notification/notification';
import { NativeStorage } from '@ionic-native/native-storage';
import { BookingData } from '../../model/booking-model';

/**
 * Generated class for the MyBookingsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-my-bookings',
  templateUrl: 'my-bookings.html',
})
export class MyBookingsPage {
  loading: Loading; // loading
  bookingList: any = []; // for booking list
  shownGroup = null; // for accordian
  currencySymbol: any = this.hotelProvider.objHotel.currency.signo; // currency symbol
  storedData: any;// locally stored data
  activeGroup = null;
  hideBackButton: boolean = false;
  constructor(public notificationModel: NotificationProvider, public navCtrl: NavController, public navParams: NavParams, private bookingProvider: BookingServiceProvider, private reservationProvider: ReservationServiceProvider, private toolsProvider: ToolsProvider, public menuCtrl: MenuController, private hotelProvider: HotelServiceProvider, private alertCtrl: AlertController, private constant: Constant, private nativeStorage: NativeStorage, private loadingCtrl: LoadingController) {
    menuCtrl.close();
    //get notification data from local storage
    this.nativeStorage.getItem('notificationData').then(notificationData => {
      if (notificationData != undefined) {
        let data = notificationData.data;
        this.storedData = data.filter(x => x.type == "reservation");
      }
    });
    //set previously viewed item
    if (this.bookingProvider.objBooking && this.bookingProvider.objBooking.activeItem != null) {
      this.activeGroup = this.bookingProvider.objBooking.activeItem;
    }
  }
  //page leave
  ionViewWillLeave() {
    this.hideBackButton = true;
    this.notificationModel.currentPageName = "";
    this.bookingProvider.objBooking.activeItem = this.activeGroup;
  }
  //page load
  ionViewDidLoad() {
    this.showLoading();
    this.hideBackButton = false;
  }
  //after page load
  ionViewWillEnter() {
    this.hideBackButton = false;
    this.notificationModel.currentPageName = "NotificationsPage";
    let reservationId = this.reservationProvider.objReservation.reservationId;
    this.bookingProvider.fnGetBookingInfo(reservationId).subscribe(data => {
      if (data.success == true) {
        if (!this.bookingProvider.objBooking) {
          this.bookingProvider.objBooking = new BookingData();
        }
        this.bookingProvider.objBooking.data = data.response;
        // store temp array
        let tempArray: any = this.bookingProvider.objBooking.data;
        let itemArray: any = [];
        let notifictnIds1: any = [];
        let notifictnIds2: any = [];
        let dataArray: any = [];
        let storeIds: any = [];
        let changedIds: any = [];
        let readStatus: any = [];

        for (let item of tempArray) {
          itemArray = item;
          itemArray.read = 0;
          dataArray.push(itemArray);
          notifictnIds2.push(item.id);
        }
        //check for local storage notifications
        if (this.storedData && this.storedData.length > 0) {

          for (let res of this.storedData) {
            storeIds[res.notificationID] = res.response.estado;
            readStatus[res.notificationID] = res.read;
            notifictnIds1.push(res.notificationID);
          }
          //for setting read or unread
          if (storeIds.length > 0) {
            let tempData = dataArray;
            dataArray = [];
            for (let list of tempData) {
              let listArray: any = [];
              listArray = list;
              if (storeIds[list.id] != undefined) {
                if (storeIds[list.id] != list.estado) {
                  listArray.read = 0;
                  changedIds.push(list);
                }
              }
              if (readStatus[list.id] != undefined && storeIds[list.id] == list.estado) {
                listArray.read = readStatus[list.id];
              }
              dataArray.push(listArray);
            }
          }
          // store new data into local storage if any
          let difference = notifictnIds2.filter(x => !notifictnIds1.includes(x));

          if (difference.length > 0) {
            let newData = [];
            for (let id of difference) {
              var x = dataArray.filter(x => x.id == id);
              newData.push(x[0]);
            }
            this.notificationModel.storeNotificationData("reservation", newData);
          }
          //if status of any booking item is chnaged then update into unread
          if (changedIds.length > 0) {
            this.notificationModel.notificationUnRead(changedIds, "reservation");
          }

        } else {
          // store into local storage
          if (data.response.length > 0) {
            this.notificationModel.storeNotificationData("reservation", this.bookingProvider.objBooking.data);
          }

        }

        this.bookingList = dataArray;
        this.bookingProvider.objBooking.data = this.bookingList;

      }
      this.loading.dismiss();
    });

  }

  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }
  //accordian toggle
  toggleGroup(group, item) {
    // set item as read
    if (this.bookingList[group].read == 0) {
      this.notificationModel.notificationRead(item.id, "reservation", item.estado);
      this.bookingList[group].read = 1;
    }
    if (this.isGroupShown(group)) {
      this.shownGroup = null;
    } else {
      this.shownGroup = group;
    }
    this.activeGroup = group;
  };
  //accordian
  isGroupShown(group) {
    return this.shownGroup === group;
  };

  activeClassFn(index) {
    return index === this.activeGroup;
  }
  //format status
  formatStatusClass(status) {
    let statusKey = {
      'C': 'confirmed',
      'P': 'pending',
      'X': 'cancelled'
    };
    return 'status ' + statusKey[status];
  }
  //format status string
  formatStatusString(status) {
    let statusKey = {
      'C': 'common.reservation.confirmed',
      'P': 'common.reservation.pending',
      'X': 'common.reservation.cancelled'
    };

    return statusKey[status];
  }
  //format price
  formatPrice(values) {

    if (values.tipoServicio == 'SERVICIO_HABITACIONES') {
      let precio = 0;
      for (let i = 0; i < values.carrito.items.length; i++) {
        precio = precio + values.carrito.items[i].precio * values.carrito.items[i].cantidad;
      }
      values.precio = precio;
      values.pax = 1;
    }

    if (!values.precio)
      return '--';

    let price: any = parseFloat(values.precio) * values.pax;
    price = (Math.round(price * 100) / 100);

    return parseFloat(price).toFixed(2) + ' ' + this.currencySymbol;

  }
  //get service type
  getServiceType(serviceType) {

    serviceType = serviceType.toUpperCase();
    let serviceTranslate = this.toolsProvider.fnLanguageTranslate(["view.restaurantCarousel.title", "view.actividadCarousel.title", "view.ofertaCarousel.title", "view.saludLearnMore.title", "view.servicioHabitacion.title"]); // language translation
    let serviceText = '';
    serviceTranslate.subscribe((value: string) => {
      serviceText = value;
    });
    switch (serviceType) {

      case this.constant.SERVICE_RESTAURANT:
        return serviceText['view.restaurantCarousel.title'];
      case this.constant.SERVICE_ACTIVITY:
        return serviceText['view.actividadCarousel.title'];
      case this.constant.SERVICE_OFFER:
        return serviceText['view.ofertaCarousel.title'];
      case this.constant.SERVICE_WELLNESS:
        return serviceText['view.saludLearnMore.title'];
      case this.constant.SERVICE_ITEM_WELLNESS:
        return serviceText['view.saludLearnMore.title'];
      case this.constant.SERVICE_ROOM:
        return serviceText['view.servicioHabitacion.title'];

    }
  }
  //get service name
  getServiceName(serviceName, serviceType) {
    if (serviceType == 'SERVICIO_HABITACIONES') {
      let serviceTranslate = this.toolsProvider.fnLanguageTranslate("view.servicioHabitacion.title"); // language translation
      let serviceText = '';
      serviceTranslate.subscribe((value: string) => {
        serviceText = value;
      });
      return serviceText;
    } else {
      return serviceName;
    }
  }
  //get long date format
  longFormatDate(date, reservationMode, turns) {

    if (reservationMode == 'T') {
      date = date.split(" ")[0];
      return date + " " + turns;
    } else if (reservationMode == 'C') {
      date = date.split(":");
      date = date[0] + ":" + date[1];
      return date;
    } else {
      date = date.split(" ")[0];
      return date;
    }

  }
  //format pax
  formatPax(pax) {
    if (pax > 0) {
      let paxTextTranslate = this.toolsProvider.fnLanguageTranslate("view.reservation.label.people.title"); // language translation
      let paxText = '';
      paxTextTranslate.subscribe((value: string) => {
        paxText = value;
      });
      return pax + ' ' + paxText;
    }
  }
  //get sevice comments
  getServiceComments(item) {

    if (!item.comentarios) {
      return '--';
    } else {
      return item.comentarios;
    }
  }
  //get frontend comments
  getFrontDeskComments(item) {

    if (!item.respuestaHotel) {
      return '--';
    } else {
      return item.respuestaHotel;
    }
  }
  //get QR code image path
  getQRCode(item) {

    let reservationId = this.reservationProvider.objReservation.reservationId;
    let QRImage = this.bookingProvider.getQRImagePath(item.qrId, reservationId),
      defaultLogo = "assets/imgs/QR_LOGO.png",
      qrId = item.qrId; // making sure the QR image is received

    if ((item.estado == 'C') && (qrId != null)) {
      //return values.QR;
      return QRImage;
    } else {
      return defaultLogo;
    }

  }
  // format booking date
  formatBookingDate(values) {

    let today = new Date(),
      reservaDate = new Date(this.toolsProvider.formatDate(values.fecha).split(" ")[0]),
      date = '';
    let dateDiff = this.toolsProvider.date_diff_indays(today, reservaDate);
    let dateTextTranslate = this.toolsProvider.fnLanguageTranslate(["common.today", "common.tomorrow"]); // language translation
    let todayText = '';
    let tomorrowText = '';
    dateTextTranslate.subscribe((value: string) => {
      todayText = value["common.today"];
      tomorrowText = value["common.tomorrow"];
    });
    if (dateDiff == 0) { //today
      date = todayText;
    } else if (dateDiff == 1) {
      date = tomorrowText;
    } else {

      let dayName = this.toolsProvider.getShortDayName(reservaDate.getDay());
      let monthName = this.toolsProvider.getShortMonthName(reservaDate.getMonth());
      let dateFormat = dayName + ' ' + reservaDate.getDate() + ' ' + monthName;
      date = dateFormat;
    }

    if (values.modoReserva == 'T')
      date += ' ' + values.turno;
    else if (values.modoReserva == 'C')
      date += ' ' + reservaDate.getHours() + ':' + reservaDate.getMinutes();

    return date;

  }
  // cancel booking request
  public fnCancelRequest(item) {
    let currentDate = new Date();
    let serviceDate = new Date(this.toolsProvider.formatDate(item.fecha).split(" ")[0]);
    let serviceId = item.id;
    let reservationId = this.reservationProvider.objReservation.reservationId;

    // control whether a confirmed call is in the past
    if ((item.estado == 'C') && (serviceDate < currentDate)) {
      console.log('Call is in the past and cannot be cancelled!');
      return;
    }
    let confirmMessages = this.toolsProvider.fnLanguageTranslate(["view.compras.cancellation.confirm.message", "buttons.no", "buttons.yes", "common.confirm"]); // language translation
    let confirmMsg = '';
    confirmMessages.subscribe((value: string) => {
      confirmMsg = value['view.compras.cancellation.confirm.message'].replace('{servicioNombre}', item.servicioNombre);
      let alertnew = this.alertCtrl.create({
        title: value['common.confirm'],
        message: confirmMsg,
        cssClass: 'cls_alrt',
        buttons: [
          {
            text: value['buttons.no'],
            role: 'cancel',
            handler: data => {

            }
          },
          {
            text: value['buttons.yes'],
            handler: data => {
              this.bookingProvider.sendCancellation(reservationId, serviceId).subscribe(data => {
                if (data.success == true) {
                  item.estado = 'X';
                }
              });

            }
          }]
      });
      alertnew.present();
      setTimeout(() => { alertnew.dismiss(); }, 5000);
    });

  }

  //function to go to previous page
  public fnBack() {
    this.navCtrl.popToRoot({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

}



