import { Injectable, ErrorHandler } from '@angular/core';
import { App, AlertController, Loading } from 'ionic-angular';
import { ToolsProvider } from '../providers/tools/tools';
/*
  Generated class for the GuestmateErrorHandlerProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class GuestmateErrorHandlerProvider implements ErrorHandler {
    loading: Loading;
    constructor(public app: App, public alertCtrl: AlertController, private tools: ToolsProvider) {

    }

    public handleError(err: any): void {
        console.log(err);
        if (err.noNetwork) {
            let msg = this.tools.fnLanguageTranslate(["common.connectionError", "common.error", "buttons.ok"]); // translate of html content with common funtion
        
            msg.subscribe((value: string) => {
                let alertnew = this.alertCtrl.create({
                    title: value['common.error'],
                    message: value['common.connectionError'],
                    cssClass:'cls_alrt',
                    buttons: [
                        {
                            text: value['buttons.ok'],
                            role: 'cancel',
                            handler: data => {
                                if (this.loading) {
                                    this.loading.dismiss();
                                }
                            }
                        }]
                });
                alertnew.present();
                setTimeout(() => { alertnew.dismiss(); }, 5000);

            });
        }
    }
}



