import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Constant } from '../../constants/constants';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { ActivityData } from '../../model/activity-model';
import { DeviceServiceProvider } from '../device-service/device-service';


/*
  Generated class for the ActivityServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ActivityServiceProvider {

  objActivity: ActivityData;

  constructor(public http: HttpClient, private constant: Constant, private communicationProvider: CommunicationServiceProvider, private deviceProvider: DeviceServiceProvider) {
    console.log('Hello ActivityServiceProvider Provider');
  }

  public getActivityList(hotelId, reservationId) {

    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/cacheable/hotel/' + hotelId + '/actividades';
      let Params = new HttpParams();
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.get(url, Params, observer); // http get service call

    });

  }

  public sendActivityReservation(serviceId, date, turn, person, comments, reservationId) {
    return Observable.create(observer => {
      let service = 'CREATE_ACTIVITY_RESERVATION'; // service type
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('serviceId', serviceId); // params
      Params = Params.append('fecha', date);
      Params = Params.append('turno', turn);
      Params = Params.append('pax', person);
      Params = Params.append('comentario', comments);
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer, service); // http get service call

    });
  }

  public sendServiceReservationWithPayment(reservationId,serviceId, date, turn, noOfPersons, comments, cardholder, token) {
    return Observable.create(observer => {
      let service = 'CREATE_ACTIVITY_RESERVATION_PAYMENT';
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('serviceId', serviceId);
      Params = Params.append('fecha', date);
      Params = Params.append('turno', turn);
      Params = Params.append('pax', noOfPersons);
      Params = Params.append('comentario', comments);
      Params = Params.append('titularTarjeta', cardholder);
      Params = Params.append('tokenTarjeta', token);
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer, service);

    });

  }

}



