import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Constant } from '../../constants/constants';
import { UpgradeRoomData } from '../../model/upgrade-room-model';
import { ReservationServiceProvider } from '../reservation-service/reservation-service';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { DeviceServiceProvider } from '../device-service/device-service';

/*
  Generated class for the UpgradeRoomServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class UpgradeRoomServiceProvider {
  objUpgradeRoom: UpgradeRoomData;

  constructor(public http: HttpClient, public constant: Constant, public reservation: ReservationServiceProvider, private communication: CommunicationServiceProvider, public deviceProvider: DeviceServiceProvider) {
    console.log('Hello UpgradeRoomServiceProvider Provider');
    this.objUpgradeRoom = new UpgradeRoomData();
  }

  public sendUpgradeRoomInfo() {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/cacheable/hotel/upgrades-habitacion';
      let Params = new HttpParams();
      Params = Params.append('reservaId', this.reservation.objReservation.reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      this.communication.get(url, Params, observer); // http get service call
    });

  }
}



