import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { Constant } from '../../constants/constants';
import { DeviceServiceProvider } from '../device-service/device-service';
import { ReservationServiceProvider } from '../reservation-service/reservation-service';
import { BookingServiceProvider } from '../booking-service/booking-service';
import { CommonServiceProvider } from '../common-service/common-service';
import { NotificationData } from '../../model/notification-model';
import { NativeStorage } from '@ionic-native/native-storage';
/*
  Generated class for the NotificationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class NotificationProvider {
  modelNotification: NotificationData;
  Registered: boolean = false;
  RegId: string = "";
  NotificationProviderId: string = "";
  ErrorMsg: any = null;
  reservationNotifications: number = 0;
  messageNotifications: number = 0;
  hasNotifications: boolean = false;
  currentPageName: string = "";
  constructor(public commonServiceProvider: CommonServiceProvider, public bookingProvider: BookingServiceProvider, public reservationModel: ReservationServiceProvider, private deviceProvider: DeviceServiceProvider, public http: HttpClient, public communicationProvider: CommunicationServiceProvider, public constant: Constant, public nativeStorage: NativeStorage) {
    console.log('Hello NotificationProvider Provider');
  }
  public sendDeviceData() {
    return Observable.create(observer => {
      let service = 'HELLO_I_AM_BUNDLE'; // url type
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('uuid', this.deviceProvider.uuid);
      Params = Params.append('locale', this.deviceProvider.preferredLanguage);
      Params = Params.append('versionApp', this.constant.VERSION_APP);
      Params = Params.append('modelo', this.deviceProvider.model);
      Params = Params.append('plataforma', this.deviceProvider.platForm);
      Params = Params.append('versionPlataforma', this.deviceProvider.osVersion);
      Params = Params.append('versionCordova', this.deviceProvider.cordovaVersion);
      Params = Params.append('bundle', this.constant.APP_DOMAIN);
      Params = Params.append('deviceNotificationId', this.RegId);
      Params = Params.append('notificationProviderDeviceId', this.NotificationProviderId);
      Params = Params.append('deviceNotificationIdProviderError', this.ErrorMsg);
      this.communicationProvider.post(url, Params, observer, service);

    });
  }
  public fetchNotifications() {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/online/huesped/mis-mensajes';
      let Params = new HttpParams();
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      Params = Params.append('reservaId', this.reservationModel.objReservation.reservationId);
      this.communicationProvider.get(url, Params, observer); // http get service call

    });

  }
  dispatchMessage(response) {
    console.log('----response----');
    console.log(response);
    // Use try because some parts of the JSON path can be null	
    try {
      if (typeof response["payload"]["additionalData"]["call_answer"] != 'undefined') {
        response.content = response.payload.additionalData.call_answer.content;
        response.title = response.payload.additionalData.call_answer.title;
        response.message = response.payload.additionalData.call_answer.message;
        //this.fireEvent('nuevanotificacion', response);
        console.log(this.getEventName(response));
        this.fireEvent(this.getEventName(response), response);
      }
      else {
        console.log("payload: " + JSON.stringify(response));
      }
    }
    catch (e) {
      console.log("payload: " + JSON.stringify(response));
    }

  }
  fireEvent(eventname, response) {
    console.log(eventname);
    switch (eventname) {
      case 'reservaconfirmada':
        this.reservaConfirmadaNotification(response);
        break
      case 'reservarechazada':
        this.reservaRechazadaNotification(response);
        break;
      case 'checkinabierto':
        this.checkinAbiertoNotification(response);
        break;
      case 'checkinconfirmado':
        this.checkinConfirmadoNotification(response);
        break;
      case 'checkinrechazado':
        this.checkinRechazadoNotification(response);
        break;
      case 'pushnotification':
        this.pushNotification(response);
        break;


    }
  }
  getEventName(response) {
    let eventName: string = "";
    switch (response.content.tipo) {
      case 'reservaRestaurante':
        if (response.content.aceptada) {
          eventName = 'reservaconfirmada';
        } else {
          eventName = 'reservarechazada';
        }
        break;
      case 'reservaOferta':
        if (response.content.aceptada)
          eventName = 'reservaconfirmada';
        else eventName = 'reservarechazada';
        break;
      case 'reservaActividad':
        if (response.content.aceptada)
          eventName = 'reservaconfirmada';
        else eventName = 'reservarechazada';
        break;
      case 'reservaServicioSalud':
        if (response.content.aceptada)
          eventName = 'reservaconfirmada';
        else eventName = 'reservarechazada';
        break;
      case 'reservaItemDeServicioSalud':
        if (response.content.aceptada)
          eventName = 'reservaconfirmada';
        else eventName = 'reservarechazada';
        break;
      case 'servicioHabitaciones':
        if (response.content.aceptada)
          eventName = 'reservaconfirmada';
        else eventName = 'reservarechazada';
        break;
      case 'checkinAbierto':
        eventName = 'checkinabierto';
        break;

      case 'peticionCheckin':
        if (response.content.aceptada)
          eventName = 'checkinconfirmado';
        else eventName = 'checkinrechazado';
        break;

      case 'servicioExtra':
        if (response.content.aceptada)
          eventName = 'reservaconfirmada';
        else eventName = 'reservarechazada';
        break;
      case 'pushNotification':
        eventName = 'pushnotification';
        break;
    }
    return eventName;
  }
  reservaConfirmadaNotification(response) {
    if (this.currentPageName == "MyBookingsPage") {
      //Reload content of MyBookingsPage
      if (this.reservationModel.objReservation.reservationId != undefined) {
        this.bookingProvider.fnGetBookingInfo(this.reservationModel.objReservation.reservationId).subscribe(data => {
          if (data.success == true) {
            this.bookingProvider.objBooking.data = data.response;
          }
        });
      }
    }
    else {
      this.addNewReservationNotification(response);
    }
    //Move the focus of MyBookingsPage to latest record (Probably register an event)
  }
  addNewReservationNotification(response) {
    this.reservationNotifications = this.reservationNotifications + 1;
    this.commonServiceProvider.objAppData.reservationNotificationsPending = this.commonServiceProvider.objAppData.reservationNotificationsPending + 1;
    this.checkForPendingNotifications();
  }
  checkForPendingNotifications() {
    var hayNotificaciones = this.commonServiceProvider.objAppData.notificationsPending || this.commonServiceProvider.objAppData.reservationNotificationsPending;
    if (hayNotificaciones) {
      this.hasNotifications = true;
    }
  }
  reservaRechazadaNotification(response) {
    this.reservaConfirmadaNotification(response);
  }
  checkinAbiertoNotification(response) {
    console.log("message notification - in - checkinAbiertoNotification");
    if (this.currentPageName != "NotificationsPage") {
      console.log("message notification - in - if - checkinAbiertoNotification");
      this.addNewMessageNotification(response);
    }
    if (this.reservationModel.objReservation.reservationId != undefined) {
      this.fetchNotifications().subscribe(data => {
        if (data.success) {
          this.modelNotification = data.response;
        }
      });
    }
  }
  addNewMessageNotification(response) {
    console.log("message notification - in - addNewMessageNotification");
    this.messageNotifications = this.messageNotifications + 1;
    this.commonServiceProvider.objAppData.notificationsPending = this.commonServiceProvider.objAppData.notificationsPending + 1;
    this.checkForPendingNotifications();

  }
  checkinConfirmadoNotification(response) {
    console.log("message notification - in - checkinConfirmadoNotification");
    this.checkinAbiertoNotification(response);
    this.reservationModel.objReservation.roomNumber = response.content.numeroHabitacion;
  }
  checkinRechazadoNotification(response) {
    console.log("message notification - in - checkinRechazadoNotification");
    this.checkinAbiertoNotification(response);
  }
  pushNotification(response) {
    console.log("message notification - in - pushNotification");
    this.checkinAbiertoNotification(response);
  }

  public notificationRead(id, type, status?) {
    this.nativeStorage.getItem('notificationData').then(notificationData => {
      if (notificationData != undefined) {
        let tempArray = notificationData.data;

        if (type == "reservation") {
          let tmpData = tempArray.filter(x => x.notificationID == id);
          tmpData[0].read = 1;
          if (this.reservationNotifications > 0 && status != 'P') {
            this.reservationNotifications = this.reservationNotifications - 1;
          }
        }
        if (type == "message") {
          let tmpData = tempArray.filter(x => x.notificationID == id);
          tmpData[0].read = 1;
          if (this.messageNotifications > 0) {
            this.messageNotifications = this.messageNotifications - 1;
          }
        }

        let messageNtfn = tempArray.filter(x => x.type == "message").filter(x => x.read == 0);
        let reservationNtfn = tempArray.filter(x => x.type == "reservation").filter(x => x.read == 0).filter(x => x.response.estado != 'P');

        if (messageNtfn.length == 0 && reservationNtfn.length == 0) {
          this.hasNotifications = false;
        }

        this.nativeStorage.setItem('notificationData', {
          'data': tempArray
        })
          .then(
            () => console.log('Stored item!'),
            error => console.error('Error storing item', error)
          );
      }
    });
  }

  public notificationUnRead(ids, type) {
    this.nativeStorage.getItem('notificationData').then(notificationData => {
      if (notificationData != undefined) {
        let tempArray = notificationData.data;

        if (type == "reservation" && this.reservationNotifications > 0) {
          for (let list of ids) {
            let tmpData = tempArray.filter(x => x.notificationID == list.id);
            tmpData[0].read = 0;
            tmpData[0].response = list;
          }
        }
        this.nativeStorage.setItem('notificationData', {
          'data': tempArray
        })
          .then(
            () => console.log('Stored item!'),
            error => console.error('Error storing item', error)
          );
      }
    });
  }

  public storeNotificationData(type, response) {

    let notificationArray: any = [];
    for (let item of response) {
      let array = {
        'type': type, 'response': item, 'notificationID': item.id
        , 'read': 0
      };
      notificationArray.push(array);
    }

    this.nativeStorage.getItem('notificationData').then(temp => {
      if (temp != undefined) {

        notificationArray = notificationArray.concat(temp.data);
        var obj = {};
        for (var i = 0, len = notificationArray.length; i < len; i++) {
          obj[notificationArray[i]['notificationID']] = notificationArray[i];
        }

        notificationArray = new Array();
        for (var key in obj) {
          notificationArray.push(obj[key]);
        }

        this.nativeStorage.setItem('notificationData', {
          'data': notificationArray
        })
          .then(
            () => console.log('Stored notificationData!'),
            error => console.error('Error storing item', error)
          );

      } else {
        this.nativeStorage.setItem('notificationData', {
          'data': notificationArray
        })
      }
    }).catch((e) => {
      this.nativeStorage.setItem('notificationData', {
        'data': notificationArray
      })
      console.log(e);
    });

  }
}


