import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DeviceServiceProvider } from '../device-service/device-service';
import { ToolsProvider } from '../../providers/tools/tools';

@Injectable()
export class CountryServiceProvider {
  url: any; // url of country list json file
  language: any; // to set device preferred language 

  constructor(public http: HttpClient, public device: DeviceServiceProvider, public tools: ToolsProvider) {
    console.log('Hello CountryServiceProvider Provider');
  }

  /**
   * Function to show country list based on device preferred language
   */
  public fnshowCountryList() {
    this.language = this.device.preferredLanguage;
    this.tools.isLanguageFromSpain(this.language) ? this.url = "assets/countries/countriesES.json" : this.url = "assets/countries/countries.json";
    return Observable.create(observer => {
      this.http.get(this.url)
        .subscribe(data => {
          observer.next(data);
          observer.complete();
          return data;
        });
    });
  }

  public fnshowProvinceList() {
    this.language = this.device.preferredLanguage;
    this.url = "assets/countries/provincies.json"
    return Observable.create(observer => {
      this.http.get(this.url)
        .subscribe(data => {
          observer.next(data);
          observer.complete();
          return data;
        });
    });
  }


}



