import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Loading } from 'ionic-angular';
import { NotificationProvider } from '../../providers/notification/notification';
import { ToolsProvider } from '../../providers/tools/tools';
import { Constant } from '../../constants/constants';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { NativeStorage } from '@ionic-native/native-storage';
import { MenuController } from 'ionic-angular';
/**
 * Generated class for the NotificationsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-notifications',
  templateUrl: 'notifications.html',
})
export class NotificationsPage {
  notificationsList: any = []; // to store notification list
  loading: Loading;//loading
  storedData: any = []; // local storage data
  hideBackButton: boolean = false;

  constructor(public menuCtrl: MenuController, public hotelService: HotelServiceProvider, public constant: Constant, public toolsProvider: ToolsProvider, public notificationModel: NotificationProvider, public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, private nativeStorage: NativeStorage) {
    this.menuCtrl.close();
    //get notification data from local storage
    this.nativeStorage.getItem('notificationData').then(notificationData => {
      if (notificationData != undefined) {
        let data = notificationData.data;
        this.storedData = data.filter(x => x.type == "message");
      }
    });

  }
  //after page load
  ionViewDidLoad() {
    this.hideBackButton = false;
    this.showLoading();
    this.notificationModel.fetchNotifications().subscribe(data => {
      if (data.success) {

        this.notificationModel.modelNotification = data.response;
        // store temp array
        let tempArray: any = this.notificationModel.modelNotification;
        let itemArray: any = [];
        let notifictnIds1: any = [];
        let notifictnIds2: any = [];
        let dataArray: any = [];
        let storeIds: any = [];

        for (let item of tempArray) {
          itemArray = item;
          itemArray.read = 0;
          dataArray.push(itemArray);
          notifictnIds2.push(item.id);
        }
        //check for local storage notifications
        if (this.storedData && this.storedData.length > 0) {

          for (let res of this.storedData) {
            storeIds[res.notificationID] = res.read;
            notifictnIds1.push(res.notificationID);
          }

          if (storeIds.length > 0) {
            let tempData = dataArray;
            dataArray = [];
            for (let list of tempData) {
              let listArray: any = [];
              listArray = list;
              if (storeIds[list.id] != undefined) {
                listArray.read = storeIds[list.id];
              }
              dataArray.push(listArray);
            }
          }
          // store new data into local storage if any
          let difference = notifictnIds2.filter(x => !notifictnIds1.includes(x));

          if (difference.length > 0) {
            let newData = [];
            for (let id of difference) {
              var x = dataArray.filter(x => x.id == id);
              newData.push(x);
            }
            this.notificationModel.storeNotificationData("message", newData);
          }
        } else {
          // store into local storage
          if (data.response.length > 0) {
            this.notificationModel.storeNotificationData("message", this.notificationModel.modelNotification);
          }

        }
        this.notificationsList = dataArray;
        this.notificationModel.modelNotification = this.notificationsList;
      }
      this.loading.dismiss();
    });

    this.notificationModel.currentPageName = "NotificationsPage";

  }
  // page leave
  ionViewWillLeave() {
    this.notificationModel.currentPageName = "";
    this.hideBackButton = true;
  }

  ionViewWillEnter() {
    this.hideBackButton = false;
  }
  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }
  //booking date formatting
  formatBookingDate(values) {

    let today = new Date(),
      reservaDate = new Date(this.toolsProvider.formatDate(values.fecha).split(" ")[0]),
      date = '';
    let dateDiff = this.date_diff_indays(today, reservaDate);
    let dateTextTranslate = this.toolsProvider.fnLanguageTranslate(["common.today", "common.tomorrow"]); // language translation
    let todayText = '';
    let tomorrowText = '';
    dateTextTranslate.subscribe((value: string) => {
      todayText = value["common.today"];
      tomorrowText = value["common.tomorrow"];
    });
    if (dateDiff == 0) { //today
      date = todayText;
    } else if (dateDiff == 1) {
      date = tomorrowText;
    } else {

      let dayName = this.toolsProvider.getShortDayName(reservaDate.getDay());
      let monthName = this.toolsProvider.getShortMonthName(reservaDate.getMonth());
      let dateFormat = dayName + ' ' + reservaDate.getDate() + ' ' + monthName;
      date = dateFormat;
    }

    if (values.modoReserva == 'T')
      date += ' ' + values.turno;
    else if (values.modoReserva == 'C')
      date += ' ' + reservaDate.getHours() + ':' + reservaDate.getMinutes();

    return date;

  }
  //got to notification details page
  goToDetails(i, notification) {
    // set item as read
    if (notification.read == 0) {
      this.notificationModel.notificationRead(notification.id, "message");
    }
    this.notificationModel.modelNotification[i].read = 1;
    this.notificationsList[i].read = 1;
    this.navCtrl.push("NotificationDetailsPage", { item: notification }, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
  }
  //difference between two days
  date_diff_indays(date1, date2) {
    return Math.floor((Date.UTC(date2.getFullYear(), date2.getMonth(), date2.getDate()) - Date.UTC(date1.getFullYear(), date1.getMonth(), date1.getDate())) / (1000 * 60 * 60 * 24));
  }
  //format date for display
  formatDate(inputDate) {
    let dateTextTranslate = this.toolsProvider.fnLanguageTranslate(["common.yesterday", "common.dateFormat.short"]); // language translation
    let yesterdayText = '';
    dateTextTranslate.subscribe((value: string) => {
      yesterdayText = value["common.yesterday"];
    });

    let today = new Date(),
      dateToCheck = new Date(this.toolsProvider.formatDate(inputDate).split(" ")[0]),
      date = '';
    let diffWithToday = this.date_diff_indays(today, dateToCheck);

    var yesterday = new Date();
    yesterday.setDate(yesterday.getDate() - 1);

    let diffWithYesterday = this.date_diff_indays(yesterday, dateToCheck);

    if (diffWithToday == 0) { //today
      date = this.toolsProvider.formatTimeToShort(inputDate);
    } else if (diffWithYesterday == 0) {
      date = yesterdayText;
    } else {
      let dayName = this.toolsProvider.getShortDayName(dateToCheck.getDay());
      let monthName = this.toolsProvider.getShortMonthName(dateToCheck.getMonth());
      let dateFormat = dayName + ' ' + dateToCheck.getDate() + ' ' + monthName;
      date = dateFormat;
    }
    return date;

  }
  //text for display getRemitente
  getRemitente(remitente, tipo) {
    let receptionTextTranslate = this.toolsProvider.fnLanguageTranslate(["common.reception"]); // language translation
    let receptionText = '';
    receptionTextTranslate.subscribe((value: string) => {
      receptionText = value["common.reception"];
    });
    if (!remitente || remitente == undefined) {
      switch (tipo) {
        case this.constant.TIPO_MENSAJE.NOTIFICACION_APERTURA_CHECKIN:
        case this.constant.TIPO_MENSAJE.NOTIFICACION_APERTURA_CHECKOUT:
        case this.constant.TIPO_MENSAJE.CHECKIN:
        case this.constant.TIPO_MENSAJE.CHECKOUT: remitente = receptionText; break;
        case this.constant.TIPO_MENSAJE.MENSAJE_PLANIFICADO: remitente = this.hotelService.objHotel.hotelName;
      }
    }
    return remitente;
  }
  //function to go to previous page
  public fnBack() {
    this.navCtrl.popToRoot({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

}



