import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Constant } from '../../constants/constants';
import { DeviceServiceProvider } from '../device-service/device-service';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { NotificationProvider } from '../notification/notification'
/*
  Generated class for the LoginServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class LoginServiceProvider {

  constructor(public notificationProvider: NotificationProvider,public http: HttpClient, public constant: Constant, public deviceProvider: DeviceServiceProvider, public communication: CommunicationServiceProvider) {
    console.log('Hello LoginProvider Provider');

  }

  public fnLoginProceed(reservationId) {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/online/huesped/registro/inicio';
      let Params = new HttpParams();
      Params = Params.append('localizador', reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      this.communication.post(url, Params, observer);

    });
  }

  public sendDeviceData() {
    return Observable.create(observer => {
      let service = 'HELLO_I_AM_BUNDLE'; // url type
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('uuid', this.deviceProvider.uuid);
      Params = Params.append('locale', this.deviceProvider.preferredLanguage);
      Params = Params.append('versionApp', this.constant.VERSION_APP);
      Params = Params.append('modelo', this.deviceProvider.model);
      Params = Params.append('plataforma', this.deviceProvider.platForm);
      Params = Params.append('versionPlataforma', this.deviceProvider.osVersion);
      Params = Params.append('versionCordova', this.deviceProvider.cordovaVersion);
      Params = Params.append('bundle', this.constant.APP_DOMAIN);
      Params = Params.append('deviceNotificationId', this.notificationProvider.RegId);
      Params = Params.append('notificationProviderDeviceId', this.notificationProvider.NotificationProviderId);
      Params = Params.append('deviceNotificationIdProviderError', this.notificationProvider.ErrorMsg);
      this.communication.post(url, Params, observer, service);

    });
  }

}



