import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events, LoadingController, Loading, Platform, normalizeURL } from 'ionic-angular';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { ToolsProvider } from '../../providers/tools/tools';
import { HomePage } from '../home/home';
import { Hotel } from '../../model/hotel-model';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { DomSanitizer } from '@angular/platform-browser';

/**
 * Generated class for the ReceptionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-reception',
  templateUrl: 'reception.html',
})
export class ReceptionPage {

  loading: Loading;
  receptionData: any;
  receptionistImageStyle: any;
  backgroundImageStyle: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public reservationPvdr: ReservationServiceProvider, public hotelPvdr: HotelServiceProvider, public tools: ToolsProvider, public events: Events, public loadingCtrl: LoadingController, private transfer: FileTransfer, private file: File, private sanitize: DomSanitizer, private platForm: Platform) {

  }
  ionViewDidLoad() {
    this.initialLoad();
  }
  initialLoad() {

    let defaultTextTranslate = this.tools.fnLanguageTranslate("view.reception.message");
    let defaultText = '';
    defaultTextTranslate.subscribe((value: string) => {
      defaultText = value;
    });
    //replace guestname and receptionist name in receptionist Text
    let receptionText = this.hotelPvdr.objHotel.receptionistText ? this.hotelPvdr.objHotel.receptionistText : defaultText;
    this.hotelPvdr.objHotel.receptionistText = receptionText.replace('{nombreHuesped}', this.reservationPvdr.objReservation.guestName).replace('{nombreRecepcionista}', this.hotelPvdr.objHotel.receptionistName);
    // receptionist image and background image set
    if (this.navParams.get("receptionistImage")) {
      this.receptionistImageStyle = this.navParams.get("receptionistImage");
    } else {
      this.downloadreceptionistImage();
    }

    if (this.navParams.get("backgroundImage")) {
      this.backgroundImageStyle = this.navParams.get("backgroundImage");
    } else {
      this.downloadbackgroundImage();
    }

  }

  downloadreceptionistImage() {
    this.receptionistImageStyle = this.tools.getImagePath(this.hotelPvdr.objHotel.receptionistImage);
    this.file.checkFile(this.file.cacheDirectory, this.hotelPvdr.objHotel.receptionistImage + ".jpg").then(() => {

      this.receptionistImageStyle = this.sanitize.bypassSecurityTrustHtml(this.file.cacheDirectory + this.hotelPvdr.objHotel.receptionistImage + ".jpg");
      this.receptionistImageStyle = this.file.cacheDirectory + this.hotelPvdr.objHotel.receptionistImage + ".jpg";
      if (this.platForm.is('ios')) {
        this.receptionistImageStyle = normalizeURL(this.file.cacheDirectory + this.hotelPvdr.objHotel.receptionistImage + ".jpg");
      }
      this.receptionistImageStyle = this.sanitize.bypassSecurityTrustHtml(this.receptionistImageStyle);

    }, (error) => {
      const fileTransfer: FileTransferObject = this.transfer.create();
      fileTransfer.download(this.receptionistImageStyle, this.file.cacheDirectory + this.hotelPvdr.objHotel.receptionistImage + ".jpg").then((entry) => {

        this.receptionistImageStyle = entry.toURL();
        if (this.platForm.is('ios')) {
          this.receptionistImageStyle = normalizeURL(entry.toURL());
        }
        this.receptionistImageStyle = this.sanitize.bypassSecurityTrustHtml(this.receptionistImageStyle);

      }, (error) => {

      });

    });
  }
  downloadbackgroundImage() {

    this.backgroundImageStyle = this.tools.getImagePath(this.hotelPvdr.objHotel.mainImage);
    this.file.checkFile(this.file.cacheDirectory, this.hotelPvdr.objHotel.mainImage + ".jpg").then(() => {

     
      this.backgroundImageStyle = this.file.cacheDirectory + this.hotelPvdr.objHotel.mainImage + ".jpg";
      if (this.platForm.is('ios')) {
        this.backgroundImageStyle = normalizeURL(this.file.cacheDirectory + this.hotelPvdr.objHotel.mainImage + ".jpg");
      }
      this.hotelPvdr.objHotel.mainImageUrl = this.backgroundImageStyle;
      this.backgroundImageStyle = this.sanitize.bypassSecurityTrustHtml(this.backgroundImageStyle);

    }, (error) => {
      const fileTransfer: FileTransferObject = this.transfer.create();
      fileTransfer.download(this.backgroundImageStyle, this.file.cacheDirectory + this.hotelPvdr.objHotel.mainImage + ".jpg").then((entry) => {

        this.backgroundImageStyle = entry.toURL();
        if (this.platForm.is('ios')) {
          this.backgroundImageStyle = normalizeURL(entry.toURL());
        }
        this.hotelPvdr.objHotel.mainImageUrl = this.backgroundImageStyle;
        this.backgroundImageStyle = this.sanitize.bypassSecurityTrustHtml(this.backgroundImageStyle);

      }, (error) => {

      });

    });

  }

  /* Profile redirection */
  public fnGotoProfile() {

    if (this.reservationPvdr.objReservation.registrationCompleted == true) {
      this.navCtrl.setRoot(HomePage, {}, { animate: true, animation: 'transition', duration: 1000, direction: 'forward' });
    } else {
      this.navCtrl.push('RegistrationPage', {}, { animate: true, animation: 'transition', duration: 1000, direction: 'forward' });
    }

  }
  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }

}



