import { Component, ChangeDetectorRef, ViewChild } from '@angular/core';
import { Events, Platform, IonicPage, NavController, NavParams, AlertController, LoadingController, Loading, Content, normalizeURL } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { ToolsProvider } from '../../providers/tools/tools';
import { LoginServiceProvider } from '../../providers/login-service/login-service';
import { HomeServiceProvider } from '../../providers/home-service/home-service';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { Globalization } from '@ionic-native/globalization';
import { Constant } from '../../constants/constants';
import { TranslateService } from '@ngx-translate/core';
import { NativeStorage } from '@ionic-native/native-storage';
import { Reservation } from '../../model/reservation-model';
import { Keyboard } from '@ionic-native/keyboard';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { Hotel } from '../../model/hotel-model';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
  providers: [Keyboard]
})
export class LoginPage {
  @ViewChild(Content) content: Content;
  loading: Loading;
  loginForm: FormGroup; // login form group
  htmlData: any = null; // to manage html content
  res: any; //to store return data while language translate
  isKeyboardOpen: boolean = false;
  receptionistImage: any;
  backgroundImage: any;
  allowOptionNoStay: any = this.constant.ALLOWOPTIONNOSTAY;
  constructor(private transfer: FileTransfer, private file: File, public hotelPvdr: HotelServiceProvider, public events: Events, private keyboard: Keyboard, public platform: Platform, public navCtrl: NavController,
    public navParams: NavParams, public formBuilder: FormBuilder, public sanitized: DomSanitizer,
    public cdRef: ChangeDetectorRef, public toolsProvider: ToolsProvider, public alertCtrl: AlertController,
    public iab: InAppBrowser, public loginProvider: LoginServiceProvider,
    public reservationProvider: ReservationServiceProvider, private globalization: Globalization,
    private translate: TranslateService, private constant: Constant, public nativeStorage: NativeStorage,
    public loadingCtrl: LoadingController, public homeProvider: HomeServiceProvider) {

    this.toolsProvider.setTranslateLanguage();
    // form initialization and validations
    this.loginForm = formBuilder.group({
      'reservationId': new FormControl(null, [Validators.required])
    });
    window.addEventListener('keyboardWillHide', (event) => {
      this.isKeyboardOpen = false;
      //this.content.scrollToBottom();
    });

  }

  fnKeyboardOpen() {
    this.isKeyboardOpen = true;
  }

  fnShowFooter() {
    this.isKeyboardOpen = false;
  }

  //initial page load
  ionViewWillEnter() {
    console.log("Device height is - " + this.platform.height());
    console.log("Device width is - " + this.platform.width());
  }
  ionViewDidEnter() {
    this.keyboard.disableScroll(true);
    //send device information to service
    this.sendDeviceDetails();
    let locale = this.constant.DEFAULT_LANGUAGE;
    this.globalization.getPreferredLanguage()
      .then((res) => {
        console.log(res);
        if (res.value.indexOf('-') !== -1) {
          locale = res.value.substring(0, 2);
        }
        else {
          locale = res.value;
        }
        if (!this.toolsProvider.isValidLanguage(locale)) {
          if (this.toolsProvider.isLanguageFromSpain(locale))
            locale = this.constant.SPANISH;
          else
            locale = this.constant.DEFAULT_LANGUAGE;

        }
        this.translate.use(locale);
        this.initialLoad();
      })
      .catch((e) => {
        console.log(e);
        locale = this.constant.DEFAULT_LANGUAGE;
        this.translate.use(locale);

      });

  }

  public initialLoad() {
    let current = this;
    this.res = this.toolsProvider.fnLanguageTranslate("view.login.label.legal"); // translate of html content with common funtion
    this.res.subscribe((value: string) => {
      this.htmlData = this.sanitized.bypassSecurityTrustHtml(value); // bypass html content to avoid losing html attributes
      this.cdRef.detectChanges(); // Checks the view and its children.

      /* click binding to termsofservice span element and redirection */
      let termsElmnt = document.querySelector('ion-nav').querySelectorAll("#termsOfService");
      for (let i = 0; i < termsElmnt.length; i++) {
        if (termsElmnt[i]) {
          termsElmnt[i].addEventListener('click', function (e) {
            current.iab.create(current.toolsProvider.getLegalUrl(), "_blank", "location=yes,toolbar=yes,hardwareback=yes");

          });
        }
      }
      /* click binding to privacyPolicy span element and redirection */
      let privacyElmnt = document.querySelector('ion-nav').querySelectorAll('#privacyPolicy');
      for (let j = 0; j < termsElmnt.length; j++) {
        if (privacyElmnt[j]) {
          privacyElmnt[j].addEventListener('click', function (e) {
            current.iab.create(current.toolsProvider.getPrivacyUrl(), "_blank", "location=yes,toolbar=yes,hardwareback=yes");
          });
        }
      }

    });

  }
  //function for sending device information to service
  public sendDeviceDetails() {
    this.loginProvider.sendDeviceData().subscribe(data => {
      if (data.success == true) {
        console.log("device information sent successfully!");
      }
    });
  }

  public checkReservationIDValid() {

    // check reservation id is null
    if (this.loginForm.get('reservationId').value == undefined || this.loginForm.get('reservationId').value == null) {
      let msg = this.toolsProvider.fnLanguageTranslate(["view.login.error.empty", "common.error", "buttons.ok"]); // translate of html content with common funtion

      msg.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: value['view.login.error.empty'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);

      });

    } else {
      let reservationId = this.loginForm.get('reservationId').value;
      this.showLoading();
      this.loginProvider.fnLoginProceed(reservationId).subscribe(data => {


        if (data.success == true) {
          // set reservation data to reservation object
          this.reservationProvider.objReservation = new Reservation();
          this.reservationProvider.objReservation.localizer = reservationId;
          this.reservationProvider.objReservation.guestName = data.response.nombreHuesped;
          this.reservationProvider.objReservation.reservationId = data.response.reservaId;
          this.reservationProvider.objReservation.email = data.response.mail;
          this.reservationProvider.objReservation.roomType = data.response.tipoHabitacion;
          this.reservationProvider.objReservation.nights = data.response.noches;
          this.reservationProvider.objReservation.noOfPersons = data.response.personas;
          this.reservationProvider.objReservation.price = data.response.precio;
          this.reservationProvider.objReservation.createdDate = data.response.entrada;
          this.reservationProvider.objReservation.alreadyRegistered = data.response.huespedYaRegistrado;
          this.reservationProvider.objReservation.registrationCompleted = data.response.formularioRegistroEnviado;
          this.reservationProvider.objReservation.tripTypeReserve = data.response.tipoViajeReservaYaDefinido;
          this.reservationProvider.objReservation.roomNumber = data.response.numeroHabitacion;
          this.reservationProvider.objReservation.hotelName = data.response.nombreHotel;
          this.reservationProvider.objReservation.alreadyCheckIn = data.response.huespedYaCheckInRealizado;
          this.reservationProvider.objReservation.alreadyVerifiedCheckOut = data.response.huespedYaCheckOutRealizado;
          this.reservationProvider.objReservation.hotelLocation = data.response.localizadorHotel;
          this.reservationProvider.objReservation.lastName = data.response.apellidos;

          this.fnAlreadyCheckIn(data);

        } else if (data) {
          // if error occurs, alert common error message
          let errmsg = this.toolsProvider.fnLanguageTranslate(["common.error", "view.login." + data.errorCode, "buttons.ok"], "view.login.error.credentials"); // translate of error message
          this.loading.dismiss();
          errmsg.subscribe((value: string) => {
            let alertnew = this.alertCtrl.create({
              title: value['common.error'],
              message: value["view.login." + data.errorCode] ? value["view.login." + data.errorCode] : value["view.login.error.credentials"],
              cssClass: 'cls_alrt',
              buttons: [
                {
                  text: value['buttons.ok'],
                  role: 'cancel'
                }]
            });
            alertnew.present();
            setTimeout(() => { alertnew.dismiss(); }, 5000);

          });

        }

      },
        error => {
          this.loading.dismiss();
        });


    }
  }

  public fnAlreadyCheckIn(dataResponse) {
    this.homeProvider.alreadyCheckIn().subscribe(data => {
      if (data.success) {
        if (data.response) {
          this.reservationProvider.objReservation.alreadyCheckIn = true;
        }
      }
      //store login data to local storage

      this.nativeStorage.setItem('reservationData', {
        'localizer': this.loginForm.get('reservationId').value, 'reservationId': dataResponse.response.reservaId, 'name': dataResponse.response.nombreHuesped, 'lastname': dataResponse.response.apellidos, 'email': dataResponse.response.mail, 'registrationCompleted': dataResponse.response.formularioRegistroEnviado, 'alreadyRegistered': dataResponse.response.huespedYaRegistrado, 'noStayGuest': false, 'response': dataResponse.response

      })
        .then(
          () => console.log('Stored item!'),
          error => console.error('Error storing item', error)
        );

      this.prepareReceptionpage();


    });
  }
  prepareReceptionpage() {
    this.hotelPvdr.objHotel = new Hotel();
    this.events.subscribe("receptionEvent", (status) => {
      if (status) {
        this.events.unsubscribe('receptionEvent');
        this.downloadreceptionistImage();
      }
    });
    let type = 'reception';
    this.reservationProvider.getHotelDetails(this.reservationProvider.objReservation.reservationId).subscribe(data => {
      if (data) {
        this.reservationProvider.getHotelInfo(data, type);
      }
    });
  }
  downloadreceptionistImage() {
    this.receptionistImage = this.toolsProvider.getImagePath(this.hotelPvdr.objHotel.receptionistImage);
    this.file.checkFile(this.file.cacheDirectory, this.hotelPvdr.objHotel.receptionistImage + ".jpg").then(() => {

      this.receptionistImage = this.file.cacheDirectory + this.hotelPvdr.objHotel.receptionistImage + ".jpg";
      if (this.platform.is('ios')) {
        this.receptionistImage = normalizeURL(this.file.cacheDirectory + this.hotelPvdr.objHotel.receptionistImage + ".jpg");
      }
      this.downloadbackgroundImage();

    }, (error) => {
      const fileTransfer: FileTransferObject = this.transfer.create();
      fileTransfer.download(this.receptionistImage, this.file.cacheDirectory + this.hotelPvdr.objHotel.receptionistImage + ".jpg").then((entry) => {

        this.receptionistImage = entry.toURL();
        if (this.platform.is('ios')) {
          this.receptionistImage = normalizeURL(entry.toURL());
        }
        this.downloadbackgroundImage();

      }, (error) => {

        this.downloadbackgroundImage();

      });

    });
  }
  downloadbackgroundImage() {

    this.backgroundImage = this.toolsProvider.getImagePath(this.hotelPvdr.objHotel.mainImage);
    this.file.checkFile(this.file.cacheDirectory, this.hotelPvdr.objHotel.mainImage + ".jpg").then(() => {

      this.backgroundImage = this.file.cacheDirectory + this.hotelPvdr.objHotel.mainImage + ".jpg";
      if (this.platform.is('ios')) {
        this.backgroundImage = normalizeURL(this.file.cacheDirectory + this.hotelPvdr.objHotel.mainImage + ".jpg");
      }
      this.fnGoToReceptionPage();

    }, (error) => {
      const fileTransfer: FileTransferObject = this.transfer.create();
      fileTransfer.download(this.backgroundImage, this.file.cacheDirectory + this.hotelPvdr.objHotel.mainImage + ".jpg").then((entry) => {

        this.backgroundImage = entry.toURL();
        if (this.platform.is('ios')) {
          this.backgroundImage = normalizeURL(entry.toURL());
        }
        this.fnGoToReceptionPage();

      }, (error) => {

        this.fnGoToReceptionPage();

      });

    });

  }
  fnGoToReceptionPage() {
    this.hotelPvdr.objHotel.mainImageUrl = this.backgroundImage;
    this.loading.dismiss();
    this.navCtrl.push("ReceptionPage", { backgroundImage: this.backgroundImage, receptionistImage: this.receptionistImage }, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
  }
  public fnGotoForgotReservation() {
    this.navCtrl.push('ForgotIdPage', {}, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
  }

  public fnGotoNoStay() {
    this.navCtrl.push('NoStayInHotelPage', {}, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
  }

  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }

}



