import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Wellness } from '../../model/wellness-model';
import { Observable } from 'rxjs';
import { Constant } from '../../constants/constants';
import { DeviceServiceProvider } from '../device-service/device-service';
import { CommunicationServiceProvider } from '../communication-service/communication-service';

/*
  Generated class for the WellnessServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class WellnessServiceProvider {

  objWellness: Wellness;

  constructor(public http: HttpClient, public constant: Constant, private deviceProvider: DeviceServiceProvider,
    public communicationProvider: CommunicationServiceProvider) {
    console.log('Hello WellnessServiceProvider Provider');

  }

  // Wellness data
  public getWellnessInfo(hotelId, reservationId) {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/cacheable/hotel/' + hotelId + '/servicios-salud';
      let Params = new HttpParams();
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.get(url, Params, observer); // http get service call

    });
  }

  // welness item data
  public getItemList(hotelId, reservationId) {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/cacheable/hotel/' + hotelId + '/servicios-salud/items';
      let Params = new HttpParams();
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.get(url, Params, observer); // http get service call

    });
  }


  public sendWellnessReservation(serviceId, date, turn, person, comments, reservationId, itemServiceId) {
    var serviceText = "";
    serviceText = (itemServiceId != null && itemServiceId != "") ? 'CREATE_HEALTH_RESERVATION_ITEM' : 'CREATE_HEALTH_RESERVATION';// service type
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER;
      let service = serviceText;
      let Params = new HttpParams();
      Params = Params.append('serviceId', serviceId); // params
      Params = Params.append('fecha', date);
      Params = Params.append('turno', turn);
      Params = Params.append('pax', person);
      Params = Params.append('comentario', comments);
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer, service); // http get service call

    });


  }

  public sendServiceWellnessWithPayment(reservationId, serviceId, date, turn, noOfPersons, comments, cardholder, token, itemServiceId) {
    return Observable.create(observer => {
      let service = "";
      service = (itemServiceId != null && itemServiceId != "") ? 'CREATE_HEALTH_RESERVATION_ITEM_PAYMENT' : 'CREATE_HEALTH_RESERVATION_PAYMENT';// service type
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('serviceId', serviceId);
      Params = Params.append('fecha', date);
      Params = Params.append('turno', turn);
      Params = Params.append('pax', noOfPersons);
      Params = Params.append('comentario', comments);
      Params = Params.append('titularTarjeta', cardholder);
      Params = Params.append('tokenTarjeta', token);
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer, service);

    });

  }

}



