import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { Constant } from '../../constants/constants';
import { HotelServiceProvider } from '../hotel-service/hotel-service';
import { ReservationServiceProvider } from '../reservation-service/reservation-service';
import { DeviceServiceProvider } from '../device-service/device-service';


@Injectable()
export class RegistrationServiceProvider {

  constructor(public http: HttpClient, public communication: CommunicationServiceProvider, public constant: Constant, public hotel: HotelServiceProvider, public reservation: ReservationServiceProvider, public deviceProvider: DeviceServiceProvider) {

  }

  /**
   * Function to complete guest data registration
   * @param name
   * @param lastName
   * @param email
   * @param country
   * @param dateOfBirth
   * @param gender
   * @param typeOfTravel
   * @param newsLetter
   * @param city
   */
  public sendFullRegistration(name, lastName, email, country, dateOfBirth, gender, typeOfTravel, newsLetter, city) {
    return Observable.create(observer => {
      let service = 'COMPLETE_REGISTER'; // service type
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      Params = Params.append('reservaId', this.reservation.objReservation.reservationId);
      Params = Params.append('nombre', name);
      Params = Params.append('apellidos', lastName);
      Params = Params.append('fechaNacimiento', dateOfBirth);
      Params = Params.append('ciudadResidencia', city);
      Params = Params.append('sexo', gender);
      Params = Params.append('codigoPaisResidencia', country);
      Params = Params.append('tipoViaje', typeOfTravel);
      Params = Params.append('newsletter', newsLetter);
      this.communication.post(url, Params, observer, service); // http post service call

    });

  }
}



