
export class Reservation {
    public id: number;
    public localizer: string;
    public hotelName: string;
    public createdDate: string;
    public roomType: string;
    public noOfPersons: number;
    public guestName: string;
    public nights: number;
    public reservationId: string;
    public alreadyRegistered: boolean;
    public registrationCompleted: boolean;
    public tripTypeReserve: boolean;
    public language: string;
    public alreadyCheckIn: boolean = false;
    public roomNumber: string;
    public alreadyVerifiedCheckOut: boolean = false;
    public email: string;
    public price: any;
    public bedrooms: number;
    public hotelLocation: string;
    public noStayGuest: boolean = false;
    public lastName:string;
    
}



