import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController, Loading, Platform } from 'ionic-angular';
import { CheckinServiceProvider } from '../../providers/checkin-service/checkin-service';
import { ToolsProvider } from '../../providers/tools/tools';
import { DomSanitizer } from '@angular/platform-browser';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { NativeStorage } from '@ionic-native/native-storage';
import { CommonServiceProvider } from '../../providers/common-service/common-service';
import { NotificationProvider } from '../../providers/notification/notification';
import { DeviceServiceProvider } from '../../providers/device-service/device-service';

/**
 * Generated class for the CheckinSummaryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-checkin-summary',
  templateUrl: 'checkin-summary.html',
})
export class CheckinSummaryPage {
  ancillaryList: any = [];
  summaryText: any;
  loading: Loading; // for loader
  serviceData: boolean;
  storedCheckinDetails: any;
  btnCntnueTriggered: any=false;
  devicePlatForm: string = "android";

  constructor(public navCtrl: NavController, public navParams: NavParams,
    private checkinProvider: CheckinServiceProvider, private toolsProvider: ToolsProvider,
    public sanitized: DomSanitizer, private reservationPvdr: ReservationServiceProvider,
    public alertCtrl: AlertController, public loadingCtrl: LoadingController, private nativeStorage: NativeStorage,
    public commonProvider: CommonServiceProvider, private notificationProvider: NotificationProvider,
    public deviceProvider: DeviceServiceProvider, private platForm: Platform
  ) {
    this.getStoredCheckinDetails();
    if (this.platForm.is('ios')) {
        this.devicePlatForm = "ios";
    }
  }

  ionViewWillEnter() {
    let summaryTextTranslate = this.toolsProvider.fnLanguageTranslate("view.checkin.template.summary");
    let summaryTextContent = '';
    summaryTextTranslate.subscribe((value: string) => {
      summaryTextContent = value;
    });
    this.reservationPvdr.objReservation.createdDate = this.reservationPvdr.objReservation.createdDate.split(' ')[0];
    //replace variable date and time with actual values
    this.summaryText = summaryTextContent.replace('{fechaEntrada}', this.reservationPvdr.objReservation.createdDate).replace('{horaPrevistaCheckin}', this.checkinProvider.objCheckin.checkinTime);
    this.summaryText = this.sanitized.bypassSecurityTrustHtml(this.summaryText);
    this.ancillaryList = this.checkinProvider.objCheckin.ancillarySummaryList;
    this.serviceData = this.ancillaryList.length > 0 ? true : false;
  }

  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }
  getStoredCheckinDetails() {
    this.nativeStorage.getItem('checkinDetails').then(Data => {
      console.log(Data);
      if (Data && Data != undefined) {
        this.storedCheckinDetails = Data;
      }
    });
  }
  //click to request check-in of guest
  fnOnRequestCheckin() {
    this.deviceProvider.btnCheckInEnabled = true; //to disable device back button click
    if (!this.btnCntnueTriggered) {
      this.btnCntnueTriggered=true;                       //to disable button to avoid multiple clicks
      this.showLoading();
      this.checkinProvider.sendCheckinData(this.storedCheckinDetails).subscribe(data => {
        let msg = this.toolsProvider.fnLanguageTranslate(["common.requestReceived", "view.checkin.success", "common.connectionError", "buttons.ok"]); //  message translation
        if (data.success == true) {
          this.loading.dismiss();
          this.commonProvider.objFormData.checkin = [];
          this.commonProvider.objFormData.checkin.checkinCompleted = true;
          this.reservationPvdr.objReservation.alreadyCheckIn = true;
          this.updateLocalStorage();
          msg.subscribe((value: string) => {
            let alertnew = this.alertCtrl.create({
              title: value['common.requestReceived'],
              message: value["view.checkin.success"],
              cssClass: 'cls_alrt',
              buttons: [
                {
                  text: value['buttons.ok'],
                  role: 'cancel',
                  handler: data => {

                    this.navCtrl.popToRoot({ animate: true, animation: 'transition', duration: 500, direction: 'forward' });
                  }
                }]
            });
            alertnew.present();
            setTimeout(() => {
              alertnew.dismiss();
              this.navCtrl.popToRoot({ animate: true, animation: 'transition', duration: 500, direction: 'forward' });
            }, 5000);
          });


        }
        else {
          this.loading.dismiss();
          // error message translate
          this.btnCntnueTriggered=false;
          msg.subscribe((value: string) => {
            let alertnew = this.alertCtrl.create({
              title: value['common.error'],
              message: value["common.connectionError"],
              cssClass: 'cls_alrt',
              buttons: [
                {
                  text: value['buttons.ok'],
                  role: 'cancel'
                }]
            });
            alertnew.present();
            setTimeout(() => { alertnew.dismiss(); }, 5000);
          });
        }
      });
      error => {
        this.btnCntnueTriggered=false;
        this.loading.dismiss();
      }
    }
  }

  //function to go to previous page
  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  public updateLocalStorage() {
    let tempData: any;
    this.nativeStorage.getItem('reservationData').then(reservation => {

      if (reservation != undefined) {
        tempData = reservation;
        tempData.response.huespedYaCheckInRealizado = true;
        this.nativeStorage.setItem('reservationData', tempData)
          .then(
          () => console.log('Local storage updated.'),
          error => console.error('Error storing item', error)
          );

      }

    });
  }

}



