import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Constant } from '../../constants/constants';
import { DeviceServiceProvider } from '../device-service/device-service';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { GuestData } from '../../model/guest-data-model';
/*
  Generated class for the NostayServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class NostayServiceProvider {

  constructor(public http: HttpClient, public constant: Constant, public deviceProvider: DeviceServiceProvider, public communication: CommunicationServiceProvider) {
    console.log('Hello NostayServiceProvider Provider');
   
  }

  public fnNoStayLoginProceed(name, surname, email, newsletter,contactphone) {
    return Observable.create(observer => {

      let url = this.constant.URL_TRANSACTION_SERVER + '/online/huesped/registro/loginnostay';
      let Params = new HttpParams();
      Params = Params.append('name', name);
      Params = Params.append('surname', surname);
      Params = Params.append('email', email);
      Params = Params.append('newsletter', newsletter);
      Params = Params.append('contactphone', contactphone);
      Params = Params.append('bundle', this.constant.APP_DOMAIN);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communication.post(url, Params, observer);

    });
  }
}



