import { Component, ViewChild } from '@angular/core';
import { Platform, Events, Nav, IonicApp } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { TranslateService } from '@ngx-translate/core'
import { Globalization } from '@ionic-native/globalization';
import { ToolsProvider } from '../providers/tools/tools';
import { DeviceServiceProvider } from '../providers/device-service/device-service';
import { Constant } from '../constants/constants';
import { NativeStorage } from '@ionic-native/native-storage';
import { LoginPage } from '../pages/login/login';
import { HomePage } from '../pages/home/home';
import { RegistrationPage } from '../pages/registration/registration';
import { ReceptionPage } from '../pages/reception/reception';
import { ReservationServiceProvider } from '../providers/reservation-service/reservation-service';
import { Reservation } from '../model/reservation-model';
import { Hotel } from '../model/hotel-model';
import { HotelServiceProvider } from '../providers/hotel-service/hotel-service';
import { Device } from '@ionic-native/device';
import { OneSignal } from '@ionic-native/onesignal';
import { NotificationProvider } from '../providers/notification/notification';
import { MyBookingsPage } from '../pages/my-bookings/my-bookings';
import { NotificationsPage } from '../pages/notifications/notifications';
import { HotelInfoPage } from '../pages/hotel-info/hotel-info';
import { CheckinSummaryPage } from '../pages/checkin-summary/checkin-summary';

declare var DeviceUUID;
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage: any = null;
  @ViewChild(Nav) nav: Nav;
  alert: any = null;
  showSplash = true;

  constructor(private NotificationService: NotificationProvider, private oneSignal: OneSignal,
    private device: Device, private globalization: Globalization, platform: Platform, private ionicApp: IonicApp,
    private statusBar: StatusBar, private splashScreen: SplashScreen, private translate: TranslateService,
    private toolsProvider: ToolsProvider, private constant: Constant,
    private deviceServiceProvider: DeviceServiceProvider, private nativeStorage: NativeStorage,
    private reservationPvdr: ReservationServiceProvider, public events: Events,
    private hotelPvdr: HotelServiceProvider) {
    platform.ready().then(() => {
      //set status bar style as default style
      //this.statusBar.overlaysWebView(false);
      //this.statusBar.styleDefault();
      this.splashScreen.hide();
      setTimeout(() => {this.showSplash=false}, 3000)
      //Set device preferred language
      this.setPreferredLanguage();
      this.events.subscribe("reservationEvent", (status) => {

        this.events.unsubscribe('reservationEvent');
        if (status) {
          this.rootPage = HomePage;
          setTimeout(() => {
            this.splashScreen.hide();
          }, 1000);
        }
      })

      this.nativeStorage.getItem('reservationData').then(reservation => {
        if (reservation != undefined && reservation.reservationId != undefined && reservation.reservationId != null) {
          this.reservationPvdr.objReservation = new Reservation();
          this.reservationPvdr.objReservation.noStayGuest = reservation.noStayGuest;
          this.reservationPvdr.objReservation.reservationId = reservation.reservationId;
          this.reservationPvdr.objReservation.guestName = reservation.name;
          this.reservationPvdr.objReservation.email = reservation.email;
          this.reservationPvdr.objReservation.lastName = reservation.lastname;
          this.reservationPvdr.objReservation.hotelLocation = reservation.response.localizadorHotel;
          this.reservationPvdr.objReservation.createdDate = reservation.response.entrada;
          this.reservationPvdr.objReservation.nights = reservation.response.noches;
          this.reservationPvdr.objReservation.noOfPersons = reservation.response.personas;
          this.reservationPvdr.objReservation.roomType = reservation.response.tipoHabitacion;
          this.reservationPvdr.objReservation.price = reservation.response.precio;
          this.reservationPvdr.objReservation.bedrooms = reservation.response.numeroHabitacion;
          this.reservationPvdr.objReservation.alreadyCheckIn = reservation.response.huespedYaCheckInRealizado;
          this.reservationPvdr.objReservation.alreadyVerifiedCheckOut = reservation.response.huespedYaCheckOutRealizado;
          //notifications
          this.nativeStorage.getItem('notificationData').then(notification => {
            let ntfArray = notification.data;
            let messageNtfn = ntfArray.filter(x => x.type == "message").filter(x => x.read == 0);
            let reservationNtfn = ntfArray.filter(x => x.type == "reservation").filter(x => x.read == 0).filter(x => x.response.estado != 'P');
            if (messageNtfn.length > 0 || reservationNtfn.length > 0) {
              this.NotificationService.hasNotifications = true;
            }
            this.NotificationService.messageNotifications = messageNtfn.length > 0 ? messageNtfn.length : 0;
            this.NotificationService.reservationNotifications = reservationNtfn.length > 0 ? reservationNtfn.length : 0;

          }).catch((err) => {
            console.log(err);
          });

          let type = '';
          if (reservation.registrationCompleted == true) {
            type = 'session';
          } else {
            type = 'newreg';
          }
          this.hotelPvdr.objHotel = new Hotel();
          this.reservationPvdr.getHotelDetails(reservation.reservationId).subscribe(data => {
            if(data){
              this.reservationPvdr.getHotelInfo(data, type);
              this.reservationPvdr.updateReservationData(reservation.reservationId);
              if (reservation.registrationCompleted != true) {
                this.rootPage = 'ReceptionPage';
                setTimeout(() => {
                  this.splashScreen.hide();
                }, 1000);
              }
            }
          });

        } else {
          this.rootPage = LoginPage;
          setTimeout(() => {
            this.splashScreen.hide();
          }, 1000);
        }
      }).catch((e) => {
        this.rootPage = LoginPage;
        setTimeout(() => {
          this.splashScreen.hide();
        }, 1000);
        console.log(e);
      });

      platform.registerBackButtonAction(() => {  // manage hardware back button press
        if (ionicApp._loadingPortal.getActive() || ionicApp._modalPortal.getActive() || ionicApp._toastPortal.getActive() || ionicApp._overlayPortal.getActive()) {
          let activePortal = ionicApp._loadingPortal.getActive() || ionicApp._modalPortal.getActive() || ionicApp._toastPortal.getActive() || ionicApp._overlayPortal.getActive();
          activePortal.dismiss();
        } else if (this.nav.getActive().component == RegistrationPage || this.nav.getActive().component == ReceptionPage) {
          this.alert = null;
          console.log('registration page back button pressed');
        } else if (this.nav.getActive().component == HomePage && this.nav.getActive().instance.menuCtrl.isOpen()) {
          this.nav.getActive().instance.menuCtrl.close();
        }
        else if (this.nav.getActive().component == NotificationsPage || this.nav.getActive().component == MyBookingsPage || this.nav.getActive().component == HotelInfoPage) {
          this.nav.popToRoot({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
        }
        else if (this.nav.getActive().component == CheckinSummaryPage && this.deviceServiceProvider.btnCheckInEnabled) {
          this.alert = null;
        }
        else if (this.nav.canGoBack()) {
          this.nav.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
        }
        else {
          this.alert = null;
          platform.exitApp();
        }
      }, 1);
      this.regPushNotification();
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
    });

  }
  regPushNotification() {
    if (this.deviceServiceProvider.Online) {

      if (!this.NotificationService.Registered) {

        this.oneSignal.setLogLevel({ logLevel: 6, visualLevel: 0 });
        this.oneSignal.startInit(this.constant.PUSH_KEY);
        //this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.InAppAlert);
        this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.Notification);
        this.oneSignal.handleNotificationReceived().subscribe((jsonData) => {
          console.log("******Notification received********");
          console.log(jsonData);
          this.NotificationService.dispatchMessage(jsonData);
        });
        this.oneSignal.handleNotificationOpened().subscribe((jsonData) => {
          console.log("*************Notification Opened*************");
          console.log(jsonData);
          this.NotificationService.dispatchMessage(jsonData);
        });
        this.oneSignal.getIds().then((ids) => {
          console.log("The notification token is: " + JSON.stringify(ids));
          //this.NotificationService.Registered=true;
          this.NotificationService.RegId = ids.userId;
          this.NotificationService.NotificationProviderId = ids.pushToken;
          this.NotificationService.sendDeviceData();
        });

        this.oneSignal.endInit();
      } else {
        console.log("Notification id already registered...");
      }
    } else {
      console.log("Device is not online...");
    }

  }
  doAction(action) {
    switch (action) {
      case 'bookings':
        this.events.publish('bookingsClick', action);
        break;
      case 'info':
        this.events.publish('infoClick', action);
        break;
      case 'home':
        this.events.publish('homeClick', action);
        break;
      case 'notifications':
        this.events.publish('notificationsClick', action);
        break;
      case 'facebook':
        this.events.publish('facebookClick', action);
        break;
      case 'twitter':
        this.events.publish('twitterClick', action);
        break;
      case 'website':
        this.events.publish('websiteClick', action);
        break;
      case 'logout':
        this.events.publish('logoutClick', action);
        break;
    }
  }
  public setPreferredLanguage() {

    let locale = this.constant.DEFAULT_LANGUAGE;
    this.globalization.getPreferredLanguage()
      .then((res) => {
        console.log(res);
        if (res.value.indexOf('-') !== -1) {
          locale = res.value.substring(0, 2);
        }
        else {
          locale = res.value;
        }
        if (!this.toolsProvider.isValidLanguage(locale)) {
          if (this.toolsProvider.isLanguageFromSpain(locale))
            locale = this.constant.SPANISH;
          else
            locale = this.constant.DEFAULT_LANGUAGE;

        }
        this.translate.use(locale);
        this.deviceServiceProvider.setPreferredLanguage(locale);
        //Set uniqueDeviceID
        this.setUniqueDeviceID();

      })
      .catch((e) => {
        console.log(e);
        locale = this.constant.DEFAULT_LANGUAGE;
        this.translate.use(locale);
        this.deviceServiceProvider.setPreferredLanguage(locale);
        //Set uniqueDeviceID
        this.setUniqueDeviceID();

      });

  }
  public setUniqueDeviceID() {
    if(this.device.uuid != null){
      this.deviceServiceProvider.setUniqueDeviceID(this.device.uuid);
    }else{
      var uuid =  new DeviceUUID().get();
      console.log("**************" + uuid)
      this.deviceServiceProvider.setUniqueDeviceID(uuid);
    }
  }
}



