import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Constant } from '../../constants/constants'
import { DeviceServiceProvider } from '../device-service/device-service';

/*
  Generated class for the ToolsProvider provider.
*/

@Injectable()
export class ToolsProvider {

  constructor(public http: HttpClient, public translate: TranslateService, public constant: Constant, public device: DeviceServiceProvider) {

  }
  public setTranslateLanguage() {
    this.translate.use(this.device.preferredLanguage);
  }

  /* used for language translation with default value */
  public fnLanguageTranslate(key, defaultvalue?) {
    this.translate.use(this.device.preferredLanguage);
    if (defaultvalue)
      return this.translate.get(key) ? this.translate.get(key) : this.translate.get(defaultvalue)

    return this.translate.get(key);
  }

  /* used for language translation with parameters */
  public fnLanguageTranslateWithParam(key, value) {
    return this.translate.get(key, value)
  }

  /* Returns true if it is a language supported by the App */
  public isValidLanguage(language) {
    if (this.constant.LANGUAGES[language])
      return true;

    return false;
  }

  /*Returns true if it is a language of Spain */
  public isLanguageFromSpain(language) {
    if (language == "es" || language == "ca" || language == "gl" || language == "eu")
      return true;

    return false;
  }

  /* Return the language es, en, fr, etc. */
  public navigatorLanguage() {
    return navigator.language ? navigator.language.split('-')[0] : navigator['userLanguage'].split('-')[0];
  }

  /* to change first letter to lower case */
  public unCapitalizeFirstLetter(string) {
    return string.charAt(0).toLowerCase() + string.slice(1);
  }

  /* Returns Privacy Policy url */
  public getPrivacyUrl() {
    var language = this.navigatorLanguage();
    if (this.isLanguageFromSpain(language))
      return this.constant.PRIVACY_URL_ES;

    return this.constant.PRIVACY_URL_WORLD;
  }

  /* Returns Terms of service url */
  public getLegalUrl() {
    var language = this.navigatorLanguage();
    if (this.isLanguageFromSpain(language))
      return this.constant.LEGAL_URL_ES;

    return this.constant.LEGAL_URL_WORLD;
  }

  public getImagePath(imageId) {

    if (imageId != null) {

      return this.constant.URL_INFO_SERVER + '/imagen/' + imageId;
    }
    else {
      return '';
    }

  }

  public formatDate(date) {
    if (date != null) {
      let dateSplit = date.split(' ');
      date = dateSplit[0];
      let time = dateSplit[1] ? dateSplit[1] : '00:00:00';
      return date.split('-')[2] + '-' + date.split('-')[1] + '-' +
        date.split('-')[0] + ' ' + time;
    } else {
      return date;
    }
  }
  public formatTimeToShort(date) {
    if (date != null) {
      let dateSplit = date.split(' ');
      let time = dateSplit[1] ? dateSplit[1] : '00:00';
      if (time != "00:00" && time != "" && time != undefined) {
        let splittedTime = time.split(":");
        if (splittedTime.length > 1) {
          time = splittedTime[0] + ":" + splittedTime[1];
        } else {
          time = splittedTime[0];
        }
        time = this.timeConvert(time);
      } else {
        time = "00:00";
      }
      return time;
    } else {
      return date;
    }
  }
  timeConvert(time) {
    // Check correct time format and split into components
    time = time.toString().match(/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];

    if (time.length > 1) { // If time format correct
      time = time.slice(1);  // Remove full string match value
      time[5] = +time[0] < 12 ? 'AM' : 'PM'; // Set AM/PM
      time[0] = +time[0] % 12 || 12; // Adjust hours
    }
    return time.join(''); // return adjusted time or original string
  }
  public dateFormatChangeForService(date) {
    if (date != null)
      return date.getDate() + '-' + (date.getMonth() + 1) + '-' + date.getFullYear() + ' 00:00:00';

    return date;
  }

  public getValidReservationDateRange(createdDate, noOfNightsStay, noStayGuest) {

    let checkInDate = new Date(this.formatDate(createdDate)); // reservation created date
    let todaysDate = new Date(); // today's date

    //for no stay guest
    if (noStayGuest == true) {
      noOfNightsStay = this.constant.MAXDAYS_BOOKING_NOSTAY;
      checkInDate = todaysDate;
    }
    //check out date
    let checkOutDate = new Date(checkInDate.getTime() + noOfNightsStay * 24 * 60 * 60 * 1000);
    //add one more day to the checkout date if the hour is greater than or equal to 23
    if (checkOutDate.getHours() >= 23) {
      checkOutDate = new Date(checkInDate.getTime() + 1 * 24 * 60 * 60 * 1000);
    }
    //time is set to 23hour 59 minutes and 59 seconds
    checkOutDate = new Date(checkOutDate.getFullYear(), checkOutDate.getMonth(),
      checkOutDate.getDate(), 23, 59, 59);

    //return null if stay is over
    if (todaysDate.getTime() >= checkOutDate.getTime())
      return null;
    if (todaysDate.getTime() > checkInDate.getTime())
      return { first: todaysDate, last: checkOutDate };

    return { first: checkInDate, last: checkOutDate };

  }

  public getShortDayName(day) {

    let daysList = this.fnLanguageTranslate("common.dayNames");
    let daysArray: any = [];

    daysList.subscribe((value: string) => {
      daysArray = value;
    });
    return daysArray[day].substring(0, 3);
  }

  public getShortMonthName(month) {

    let monthList = this.fnLanguageTranslate("common.monthNames");
    let monthArray: any = [];

    monthList.subscribe((value: string) => {
      monthArray = value;
    });
    return monthArray[month].substring(0, 3);
  }

  //Function to get the Current Date
  public fnGetToday() {
    let today: any = new Date();
    let dd: any = today.getDate();
    let mm: any = today.getMonth() + 1; //January is 0!
    let yyyy: any = today.getFullYear();

    if (dd < 10) {
      dd = '0' + dd;
    }
    if (mm < 10) {
      mm = '0' + mm;
    }
    today = yyyy + '-' + mm + '-' + dd;
    return today;
  }

  public fnDateFormatChange(date) {

    let dd: any = date.getDate();
    let mm: any = date.getMonth() + 1; //January is 0!
    let yyyy: any = date.getFullYear();
    if (dd < 10) {
      dd = '0' + dd;
    }
    if (mm < 10) {
      mm = '0' + mm;
    }

    date = yyyy + '-' + mm + '-' + dd;
    return date;
  }

  public date_diff_indays(date1, date2) {
    return Math.floor((Date.UTC(date2.getFullYear(), date2.getMonth(), date2.getDate()) - Date.UTC(date1.getFullYear(), date1.getMonth(), date1.getDate())) / (1000 * 60 * 60 * 24));
  }

  public getDayName(day) {

    let daysList = this.fnLanguageTranslate("common.dayNames");
    let daysArray: any = [];

    daysList.subscribe((value: string) => {
      daysArray = value;
    });
    return daysArray[day];
  }

  public getMonthName(month) {

    let monthList = this.fnLanguageTranslate("common.monthNames");
    let monthArray: any = [];

    monthList.subscribe((value: string) => {
      monthArray = value;
    });
    return monthArray[month];
  }

  public getQRImagePath(qrId, reservationId) {

    return this.constant.URL_INFO_SERVER + "/qr/" + qrId + "/" + this.device.uuid + "/" + reservationId + "/";
  }

  public fnSetStartDate(lastTimeInterval) {
    let defaultDate: Date = new Date();
    let endTimeTokens = lastTimeInterval.split(':');
    let endDate = new Date(defaultDate.getFullYear(), defaultDate.getMonth(), defaultDate.getDate(), endTimeTokens[0], endTimeTokens[1]);//date format

    //if last time interval is past current hour and minute 
    if ((endDate.getHours() < defaultDate.getHours()) || ((endDate.getHours() == defaultDate.getHours()) && (endDate.getMinutes() <= defaultDate.getMinutes()))) {
      //add one day to today and set that as minimum date for booking 
      let today = this.fnGetToday();
      let formattedDate = new Date(today);
      formattedDate.setDate(formattedDate.getDate() + 1);
      return this.fnDateFormatChange(formattedDate);
    }
    else {
      return this.fnGetToday();
    }
  }
}




