import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Constant } from '../../constants/constants';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { RoommakeupData } from '../../model/roommakeup-model';
import { DeviceServiceProvider } from '../device-service/device-service';
import { Storage } from '@ionic/storage';

//Xevi
import { ToastController } from 'ionic-angular';


/*
  Generated class for the ActivityServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class RoommakeupServiceProvider {

  objRoommakeup: RoommakeupData;

  constructor(public http: HttpClient, private constant: Constant, private communicationProvider: CommunicationServiceProvider, private deviceProvider: DeviceServiceProvider, private storage: Storage,
  private toastCtrl: ToastController) {
    console.log('Hello RoommakeupServiceProvider Provider');
  }

  public sendDontDisturbData(rqtype, time, comments, roomnum, reservationId) {
    return Observable.create(observer => {
      let service = 'CREATE_DONTDISTURB';
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('rqtype', rqtype); // params
      Params = Params.append('comments', comments);
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('roomnum', roomnum);
      Params = Params.append('time', time);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer, service); // http get service call
    })
  }

  public async getDontDisturbTime() {
    return await this.storage.get('dontdisturbTime');
  }

  public sendRoommakeupData(rqtype, comments, roomnum, reservationId) {
    return Observable.create(observer => {
      let service = 'CREATE_ROOMMAKEUP'; // service type
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('rqtype', rqtype); // params
      Params = Params.append('comments', comments);
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('roomnum', roomnum);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer, service); // http get service call
    });
  }

  //Xevi
  private fndebug(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.onDidDismiss(() => {
    });

    toast.present();
  }

}



