import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { CheckinData } from '../../model/checkin-model';
import { Observable } from 'rxjs';
import { Constant } from '../../constants/constants';
import { DeviceServiceProvider } from '../device-service/device-service';
import { ReservationServiceProvider } from '../reservation-service/reservation-service';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { NativeStorage } from '@ionic-native/native-storage';

/*
  Generated class for the CheckinServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class CheckinServiceProvider {
  plainObj: any = [];
  tempAncillarylst: any = [];
  objCheckin: CheckinData;
  checkinDataArray: any = [];

  constructor(public http: HttpClient, public constant: Constant, public deviceProvider: DeviceServiceProvider,
    public reservation: ReservationServiceProvider, public communication: CommunicationServiceProvider,
    private nativeStorage: NativeStorage) {
    console.log('Hello CheckinServiceProvider Provider');
    //this.objCheckin = new CheckinData();
    this.plainObj = {
      huespedes: [],
      serviciosExtra: [],
      agreeCheckboxField: null,
      asignarTarjetaCreditoATodasLasHabitaciones: null,
      codigoSeguridadTarjetaCredito: null,
      comentario: "",
      fechaCaducidadTarjetaCredito: null,
      horaPrevistaCheckin: "",
      numeroTarjetaCredito: null,
      tipoTarjetaCredito: null,
      titularTarjeta: "",
      tokenTarjeta: "",
      upgradeTipoHabitacionId: null
    };
  }
  //service call to request check-in
  public sendCheckinData(storedCheckinDetails) {

    return Observable.create(observer => {
      if (this.objCheckin != null) {
        this.plainObj.agreeCheckboxField = this.objCheckin.agreeCheckboxField;
        this.plainObj.asignarTarjetaCreditoATodasLasHabitaciones = this.objCheckin.assignCreditCardToTheRooms;
        this.plainObj.comentario = this.objCheckin.comments;
        this.plainObj.horaPrevistaCheckin = this.objCheckin.checkinTime;

        this.plainObj.fechaCaducidadTarjetaCredito = null;
        this.plainObj.codigoSeguridadTarjetaCredito = null;
        this.plainObj.numeroTarjetaCredito = null;
        this.plainObj.tipoTarjetaCredito = null;
        this.plainObj.titularTarjeta = this.objCheckin.cardHolder;
        this.plainObj.tokenTarjeta = this.objCheckin.token;

        this.plainObj.upgradeTipoHabitacionId = this.objCheckin.ancillarySummaryList.filter(x => x.type == "room").length > 0 ? this.objCheckin.ancillarySummaryList.filter(x => x.type == "room")[0].roomId : 0;//Room Id selected if room to be upgraded
        this.tempAncillarylst = this.objCheckin.ancillarySummaryList.filter(x => x.type == "service");
        this.plainObj.serviciosExtra = [];
        this.plainObj.huespedes = [];

        for (var i = 0; i < this.tempAncillarylst.length; i++) {
          let itemArray: any = {};
          itemArray.cantidad = this.tempAncillarylst[i].quantity;
          itemArray.precio = this.tempAncillarylst[i].pricePerDay;
          itemArray.servicioExtraId = this.tempAncillarylst[i].serviceId;
          itemArray.noches = this.reservation.objReservation.nights;
          itemArray.reservaId = this.reservation.objReservation.reservationId;
          this.plainObj.serviciosExtra[i] = itemArray;
        }
        for (var j = 0; j < this.objCheckin.birthDate.length; j++) {
          let itemArray: any = {};
          itemArray.addressCity = this.objCheckin.addressCity[j];
          itemArray.addressCountry = this.objCheckin.addressCountry[j];
          itemArray.addressPostalCode = this.objCheckin.addressPostalCode[j];
          itemArray.addressStreet = this.objCheckin.addressStreet[j];
          itemArray.apellidos = this.objCheckin.surname[j];

          itemArray.documentoIdentidad = this.objCheckin.idNumber[j];
          itemArray.fechaDocumentoIdentidad = this.objCheckin.dateOfIssue[j];
          itemArray.fechaNacimiento = this.objCheckin.birthDate[j];
          itemArray.mail = this.reservation.objReservation.email;
          itemArray.nacionalidadDocumentoIdentidad = this.objCheckin.nationality[j];
          itemArray.nombre = this.objCheckin.firstName[j];
          itemArray.pictureFront = this.objCheckin.pictureFront[j];
          itemArray.principal = true;
          itemArray.residencia = this.objCheckin.cityResidence[j];
          itemArray.sexo = this.objCheckin.sex[j];
          itemArray.tipoDocumentoIdentidad = this.objCheckin.identityDocument[j];
          itemArray.reservaId = this.reservation.objReservation.reservationId;
          this.plainObj.huespedes[j] = itemArray;

        }
      }
      let checkinData: any = {
        'reservationID': this.reservation.objReservation.reservationId,
        'guests': this.plainObj.huespedes[0]
      };
      let checkData = this.prepareData(checkinData, storedCheckinDetails);
      this.nativeStorage.setItem('checkinDetails', checkData);

      let url = this.constant.URL_TRANSACTION_SERVER + '/online/checkin?uuid=' + this.deviceProvider.uuid + '&reservaId=' + this.reservation.objReservation.reservationId;
      this.communication.post(url, this.plainObj, observer); // http post service call
    });
  }
  prepareData(checkData, storedCheckinDetails) {
    if (storedCheckinDetails != null && storedCheckinDetails.length > 0) {
      storedCheckinDetails.push(checkData);
      return storedCheckinDetails;
    } else {
      return [checkData];
    }
  }

  public storeCheckinData(objFormData) {
    if (objFormData.Name[0] != "" && objFormData.Name[0] != undefined) {
      console.log(objFormData);
      let itemData: any = [];
      for (var j = 0; j < objFormData.NoOfGuests; j++) {
        let itemArray: any = {};
        itemArray.Address = objFormData.Address[j];
        itemArray.City = objFormData.City[j];
        itemArray.Country = objFormData.Country[j];
        itemArray.DOB = objFormData.DOB[j];
        itemArray.DateOfIssue = objFormData.DateOfIssue[j];
        itemArray.Document = objFormData.Document[j];
        itemArray.Gender = objFormData.Gender[j];
        itemArray.LastName = objFormData.LastName[j];
        itemArray.Name = objFormData.Name[j];
        itemArray.Nationality = objFormData.Nationality[j];
        itemArray.Residence = objFormData.Residence[j];
        itemArray.ZipCode = objFormData.ZipCode[j];
        itemArray.idNumber = objFormData.idNumber[j];
        itemArray.pictureFront = objFormData.pictureFront[j];

        itemData[j] = itemArray;

      }
       
      let dataArray: any = [];
      let guestData = {
        'reservationID': this.reservation.objReservation.reservationId,
        'guests': itemData,
        'checkinCompleted': objFormData.checkinCompleted,
        'ActiveIndex': objFormData.ActiveIndex,
        'PageCounter': objFormData.PageCounter,
        'specialRequest': objFormData.specialRequest,
        'arrivalTime': objFormData.arrivalTime,
        'cardHolder': objFormData.cardHolder,
        'agreeTerms': objFormData.agreeTerms,
        'assignCard': objFormData.assignCard,
      };
     
      dataArray.push(guestData);
      this.nativeStorage.getItem('checkinPreEnteredDetails').then(temp => {
        if (temp != undefined) {

          dataArray = temp.guestData.concat(dataArray);
          var obj = {};
          for (var i = 0, len = dataArray.length; i < len; i++) {
            obj[dataArray[i]['reservationID']] = dataArray[i];
          }

          dataArray = new Array();
          for (var key in obj) {
            dataArray.push(obj[key]);
          }

          this.nativeStorage.setItem('checkinPreEnteredDetails', {
            'guestData': dataArray
          })
            .then(
              () => console.log('Stored checkinPreEnteredDetails!'),
              error => console.error('Error storing item', error)
            );

        } else {
          this.nativeStorage.setItem('checkinPreEnteredDetails', {
            'guestData': dataArray
          })
        }
      }).catch((e) => {
        this.nativeStorage.setItem('checkinPreEnteredDetails', {
          'guestData': dataArray
        })
        console.log(e);
      });

    }
  }
}



