import { Component } from '@angular/core';
import { NavController, NavParams, Loading, LoadingController, ViewController } from 'ionic-angular';
import { Http } from '@angular/http';
import { CountryServiceProvider } from '../../providers/country-service/country-service';

/**
 * Generated class for the AutoCompleteCountryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-auto-complete-country',
  templateUrl: 'auto-complete-country.html',
})
export class AutoCompleteCountryPage {
  loading: Loading;
  autocompleteItems: any;
  autocomplete: any;
  selectedCountry: string;

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public viewCtrl: ViewController, public http: Http, public countryService: CountryServiceProvider) {
    this.selectedCountry = navParams.get('selectedItem')
    this.fnGetCountryList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AutoCompleteCountryPage');
  }

  public chooseItem(item: any) {
    this.viewCtrl.dismiss(item);
  }

  /**
   * Function to load country list
   */
  public fnGetCountryList() {
    this.showLoading();
    this.countryService.fnshowCountryList().subscribe(data => {
      this.autocompleteItems = data.Country;
      this.loading.dismiss();
    });
  }

  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }


}



