import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicModule } from 'ionic-angular';
import { GuestmateErrorHandlerProvider } from '../errorhandler/guestmate-error-handler';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HTTP_INTERCEPTORS, HttpClientModule, HttpClient } from '@angular/common/http';
import { InterceptedHttp } from "../interceptor/http.interceptor";
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { ToolsProvider } from '../providers/tools/tools';
import { Globalization } from '@ionic-native/globalization';
import { DeviceServiceProvider } from '../providers/device-service/device-service';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { AutoCompleteCountryPage } from '../pages/auto-complete-country/auto-complete-country';
import { HttpModule } from '@angular/http';
import { Constant } from '../constants/constants';
import { UniqueDeviceID } from '@ionic-native/unique-device-id';
import { ReservationServiceProvider } from '../providers/reservation-service/reservation-service';
import { LoginServiceProvider } from '../providers/login-service/login-service';
import { CommunicationServiceProvider } from '../providers/communication-service/communication-service';
import { HotelServiceProvider } from '../providers/hotel-service/hotel-service';
import { CountryServiceProvider } from '../providers/country-service/country-service';
import { SearchpipePipe } from '../pipes/searchpipe/searchpipe';
import { DatePipe } from '@angular/common';
import { RegistrationServiceProvider } from '../providers/registration-service/registration-service';
import { HomeServiceProvider } from '../providers/home-service/home-service';
import { NostayServiceProvider } from '../providers/nostay-service/nostay-service';
import { NativeStorage } from '@ionic-native/native-storage';
import { IonicStorageModule } from '@ionic/storage';
import { LoginPage } from '../pages/login/login';
import { GuestServiceProvider } from '../providers/guest-service/guest-service';
import { ActivityServiceProvider } from '../providers/activity-service/activity-service';
import { CommonServiceProvider } from '../providers/common-service/common-service';
import { NgxStripeModule } from 'ngx-stripe';
import { CheckinServiceProvider } from '../providers/checkin-service/checkin-service';
import { Camera } from '@ionic-native/camera';
import { AncillaryServiceProvider } from '../providers/ancillary-service/ancillary-service';
import { Device } from '@ionic-native/device';
import { AutoCompleteSpainProvincesPage } from '../pages/auto-complete-spain-provinces/auto-complete-spain-provinces';
import { RestaurantServiceProvider } from '../providers/restaurant-service/restaurant-service';
import { CheckoutServiceProvider } from '../providers/checkout-service/checkout-service';
import { WellnessServiceProvider } from '../providers/wellness-service/wellness-service';
import { PromotionsServiceProvider } from '../providers/promotions-service/promotions-service';
import { RoomsServiceProvider } from '../providers/rooms-service/rooms-service';
import { UpgradeRoomServiceProvider } from '../providers/upgrade-room-service/upgrade-room-service';
import { BookingServiceProvider } from '../providers/booking-service/booking-service';
import { RoomissueServiceProvider } from '../providers/roomissue-service/roomissue-service';
import { RoommakeupServiceProvider } from '../providers/roommakeup-service/roommakeup-service';
import { AmenitiesServiceProvider } from '../providers/amenities-service/amenities-service';
import { NotificationProvider } from '../providers/notification/notification';
import { OneSignal } from '@ionic-native/onesignal';
import { HTTP } from '@ionic-native/http';

import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { RoomServiceExtraProvider } from '../providers/room-service-extra/room-service-extra';

export function setTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/locales/', '.json');
}

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    AutoCompleteCountryPage,
    SearchpipePipe,
    AutoCompleteSpainProvincesPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    HttpModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot(),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (setTranslateLoader),
        deps: [HttpClient]
      }
    }),
    NgxStripeModule.forRoot(''),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    AutoCompleteCountryPage,
    AutoCompleteSpainProvincesPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    HttpClient,
    { provide: ErrorHandler, useClass: GuestmateErrorHandlerProvider },
    { provide: HTTP_INTERCEPTORS, useClass: InterceptedHttp, multi: true },
    ToolsProvider,
    InAppBrowser,
    Globalization,
    DeviceServiceProvider,
    Constant,
    UniqueDeviceID,
    ReservationServiceProvider,
    LoginServiceProvider,
    Constant,
    UniqueDeviceID,
    CommunicationServiceProvider,
    HotelServiceProvider,
    CountryServiceProvider,
    SearchpipePipe,
    DatePipe,
    RegistrationServiceProvider,
    HomeServiceProvider,
    NostayServiceProvider,
    NativeStorage,
    GuestServiceProvider,
    ActivityServiceProvider,
    CommonServiceProvider,
    CheckinServiceProvider,
    Camera,
    AncillaryServiceProvider,
    Device,
    RestaurantServiceProvider,
    WellnessServiceProvider,
    WellnessServiceProvider,
    CheckoutServiceProvider,
    WellnessServiceProvider,
    RoomissueServiceProvider,
    AmenitiesServiceProvider,
    RoommakeupServiceProvider,
    PromotionsServiceProvider,
    RoomsServiceProvider,
    UpgradeRoomServiceProvider,
    BookingServiceProvider,
    NotificationProvider,
    OneSignal,
    FileTransfer,
    File,
    RoomServiceExtraProvider,
    RoomServiceExtraProvider,
    HTTP

  ]
})
export class AppModule { }



