import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { App, AlertController, MenuController, Platform } from 'ionic-angular';
import { ToolsProvider } from '../../providers/tools/tools';
import { NativeStorage } from '@ionic-native/native-storage';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { LoginPage } from '../../pages/login/login';
import { ServiceForm } from '../../model/service-form-model';
import { AppData } from '../../model/appdata-model';
import { Constant } from '../../constants/constants';
import { DeviceServiceProvider } from '../device-service/device-service';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { ActivityServiceProvider } from '../activity-service/activity-service';
import { CheckinServiceProvider } from '../checkin-service/checkin-service';
import { RoomsServiceProvider } from '../rooms-service/rooms-service';
import { PreEnteredFormData } from '../../model/pre-entered-form-data-model';

/*
  Generated class for the CommonServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class CommonServiceProvider {

  objServiceForm: ServiceForm; // object for service's common form
  objAppData: any = new AppData(); // object of appData model 
  alertlogout: any = null; // variable for logout alert box
  objFormData: PreEnteredFormData; // object of booking form model

  constructor(public http: HttpClient, public menuCtrl: MenuController, public tools: ToolsProvider,
    public hotel: HotelServiceProvider, public alertCtrl: AlertController, public nativeStorage: NativeStorage,
    public reservationProvider: ReservationServiceProvider, private app: App, private constant: Constant,
    private deviceProvider: DeviceServiceProvider, private communicationProvider: CommunicationServiceProvider,
    private activityProvider: ActivityServiceProvider, private checkinProvider: CheckinServiceProvider,
    private roomsProvider: RoomsServiceProvider, private platForm: Platform) {

    this.objFormData = new PreEnteredFormData();
    console.log('Hello CommonServiceProvider Provider');
  }

  //main menu function
  public doAction(action) {
    switch (action) {
      case 'home':

        this.menuCtrl.close();
        this.app.getActiveNav().popToRoot({ animate: true, animation: 'transition', duration: 500, direction: 'back' });

        break;
      case 'info':
        this.app.getActiveNav().push("HotelInfoPage", {}, { animate: true, animation: 'transition', duration: 500, direction: 'back' });
        break;
      case 'bookings':
        this.app.getActiveNav().push("MyBookingsPage", {}, { animate: true, animation: 'transition', duration: 500, direction: 'back' });

        break;
      case 'notifications':
        this.app.getActiveNav().push("NotificationsPage", {}, { animate: true, animation: 'transition', duration: 500, direction: 'back' });
        break;
      case 'facebook':
        if (this.platForm.is('ios')) {
          window.open('fb://page/?id=' + this.hotel.objHotel.userFacebook, "_system", "location=no");
        } else {
          window.open('fb://page/' + this.hotel.objHotel.userFacebook, "_system", "location=no");
        }
        break;
      case 'twitter':
        window.open('twitter://user?screen_name=' + this.hotel.objHotel.twitterUser, '_system', 'location=no');
        break;
      case 'website':
        window.open("http://" + this.hotel.objHotel.urlWeb, "_system", "location=yes")
        break;
      case 'logout':
        this.fnLogout();
        break;
    }
  }

  public fnLogout() { // function for logout

    let msg = this.tools.fnLanguageTranslate(["common.logoff", "common.confirm", "buttons.ok", "buttons.cancel"]); // translate of html content with common funtion
    let me = this;
    msg.subscribe((value: string) => {
      if (this.alertlogout == null) {
        this.alertlogout = this.alertCtrl.create({
          title: value['common.confirm'],
          message: value['common.logoff'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.cancel'],
              role: 'cancel',
              handler: data => {
                if (this.alertlogout != null) {
                  this.alertlogout = null;
                }
              }
            },
            {
              text: value['buttons.ok'],
              handler: data => {

                me.menuCtrl.close();
                this.alertlogout = null;
                me.nativeStorage.remove('reservationData').then(data => {
                  this.hotel.objHotel = null;
                  this.reservationProvider.objReservation = null;
                  this.activityProvider.objActivity = null;
                  this.roomsProvider.objRoomService = null;
                  this.checkinProvider.objCheckin = null;
                  this.objFormData.checkin = null;
                  me.nativeStorage.remove('notificationData').then(data => {
                  }, er => {
                    console.log(er);
                  });
                  me.nativeStorage.remove('checkinPreEnteredDetails').then(data => {
                  }, er => {
                    console.log(er);
                  });
                  // let a:any=[];
                  // this.checkinProvider.objCheckin = a;
                  this.app.getActiveNav().setRoot(LoginPage, {}, { animate: true, animation: 'transition', duration: 1000, direction: 'back' });

                }, error => {
                  console.log(error);
                });
              }
            }]
        });
        this.alertlogout.present();
        setTimeout(() => {
          if (this.alertlogout != null) {
            this.alertlogout.dismiss();
            this.alertlogout = null;
          }

        }, 5000);
      }

    });

  }
  //get terms and conditions text
  public getTermsAndConditions(reservationId) {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/online/policies/conditions';
      let Params = new HttpParams();
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('locale', this.deviceProvider.preferredLanguage);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer); // http get service call

    });
  }
  // get cancellation policy text
  public getCancellationPolicy(reservationId) {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/online/policies/cancellation';
      let Params = new HttpParams();
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('locale', this.deviceProvider.preferredLanguage);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer); // http get service call

    });
  }

  public getDatesBetween(): Date[] {
    let from: Date = new Date();
    let to: Date = this.calculateMaxDate();

    let dates = [];
    // Strip hours minutes seconds etc.
    let currentDate = new Date(
      from.getFullYear(),
      from.getMonth(),
      from.getDate()
    );

    while (currentDate <= to) {

      let dayName = this.tools.getShortDayName(currentDate.getDay());
      let monthName = this.tools.getShortMonthName(currentDate.getMonth());
      let dateFormat = dayName + ' ' + currentDate.getDate() + ' ' + monthName;
      dates.push({ dateValue: currentDate, displayDate: dateFormat });

      currentDate = new Date(
        currentDate.getFullYear(),
        currentDate.getMonth(),
        currentDate.getDate() + 1, // Will increase month if over range
      );
    }
    return dates;

  }

  public calculateMaxDate() {
    //change created date format into standard format
    let splitDate = this.tools.formatDate(this.reservationProvider.objReservation.createdDate).split(" ");
    let createdDate = new Date(splitDate[0]);
    let noOfDaysToAdd: any; // no of nights stay
    if (this.reservationProvider.objReservation.noStayGuest) {
      noOfDaysToAdd = this.constant.MAXDAYS_BOOKING_NOSTAY;
    } else {
      noOfDaysToAdd = this.reservationProvider.objReservation.nights; // no of nights stay
    }
    return (new Date(createdDate.getTime() + noOfDaysToAdd * 24 * 60 * 60 * 1000));
  }

  //create time slots array function
  public fnConvertToTimeSlotsArray(startTime, endTime, increment, today?) {

    let hourDecimal = parseInt(increment) / 60;
    let turns: any = '';
    let defaultDate: Date = new Date();
    let nextDate = new Date();
    let newDate: any;
    let timeSlots: any = [];
    let startMinute = 0, startReminder = 0, incrHour = 0;

    startReminder = defaultDate.getMinutes() % 10;
    startMinute = (defaultDate.getMinutes() - startReminder) + 10;

    //if calculated time is in the next hour,set values accordingly
    if (startMinute == 60) {
      startMinute = 0;
      incrHour = defaultDate.getHours() + 1;
    }

    //to calculate the time to start in case none passed as argument
    if ((startTime == "") || (startTime == undefined)) {
      startTime = (incrHour == 0 ? defaultDate.getHours() : incrHour) + ":" + startMinute;
      startTime = startTime.replace(":0", ":00");
      increment = 10;
      hourDecimal = parseInt(increment) / 60;
    }

    //to calculate the time to end in case none passed as argument
    if ((endTime == "") || (endTime == undefined)) {
      endTime = "23:50";
      increment = 10;
      hourDecimal = parseInt(increment) / 60;
    }
    //starting and ending time 
    let timeSlotStart = startTime;
    let timeSlotEnd = endTime;

    timeSlots.push(startTime);//push start time to array
    // end time to timedata
    let endTimeTokens = endTime.split(':');
    let endDate = new Date(defaultDate.getFullYear(), defaultDate.getMonth(), defaultDate.getDate(), endTimeTokens[0], endTimeTokens[1]);//date format
    let endHour = endDate.getHours() == 0 ? 24 : endDate.getHours();

    // start time to timedata
    let startTimeTokens = startTime.split(':');
    let startDate = new Date(defaultDate.getFullYear(), defaultDate.getMonth(), defaultDate.getDate(), startTimeTokens[0], startTimeTokens[1]);//date format
    let startHour = startDate.getHours();

    turns = (endHour - startHour) / hourDecimal;    //to calculate number of time intervals that should be shown
    for (let i = 0; i < turns; i++) {

      var timeTokens = startTime.split(':');
      newDate = new Date(defaultDate.getFullYear(), defaultDate.getMonth(), defaultDate.getDate(), timeTokens[0], timeTokens[1]);//date format

      nextDate = new Date(newDate.getTime() + (1000 * 60 * increment));//get incremented date time
      startTime = nextDate.getHours() + ":" + nextDate.getMinutes();//extract time part
      startTime = startTime.replace(":0", ":00");
      timeSlots.push(startTime);//push time to array
    }
    //if the selected date is today then go to the function
    if (today == true) {
      timeSlots = this.fnSliceExtraTimeSlots(timeSlots, timeSlotStart, timeSlotEnd, increment);
    }
    return timeSlots; // returns time slots as array
  }
  //send message
  public sendMessage(comment) {
    return Observable.create(observer => {
      let url: string = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('reservaId', this.reservationProvider.objReservation.reservationId);
      Params = Params.append('comentario', comment);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer, 'SEND_MESSAGE');
    });
  }

  public fnSliceExtraTimeSlots(timeSlots, startTime, endTime, increment) {

    let endHour = endTime.split(':')[0]; //end time hour
    let startHour = startTime.split(':')[0]; // start time hour
    let currentHour: any = new Date().getHours(); // current hour
    let currentMinute: any = new Date().getMinutes(); // current minute
    //if current hour is less than ending hour then remove extra slots from array
    if (currentHour < endHour) {
      let hourDecimal = parseInt(increment) / 60;
      let turns = (endHour - startHour) / hourDecimal;
      if (currentHour > startHour) {
        turns = (endHour - currentHour) / hourDecimal;
      }
      turns = turns + 1; // including start time count
      let totalTurns = timeSlots.length;
      let diff = totalTurns - turns;
      timeSlots.splice(0, diff);
      let indexArray: any = [];

      for (let item of timeSlots) {
        let initialTime = item.split(':');
        if (initialTime[0] == currentHour && currentMinute > initialTime[1]) {
          if (timeSlots.indexOf(item) > -1) {
            const index: number = timeSlots.indexOf(item);
            if (index !== -1) {
              indexArray.push(index);
            }
          }
        }
      }
      let newLen = indexArray.length;
      if (newLen > 0) {
        timeSlots.splice(0, newLen);
      }
    } else {
      timeSlots = [];
    }

    return timeSlots;
  }

  public getTimeIntervalWithAMPM() {
    let defaultDate: any = new Date();
    let timeArray: any = [];
    let startTime: any = "00:30";
    let increment: any = 30;
    let hourDecimal = parseInt(increment) / 60;
    let startHour = 0;
    let endHour = 23;
    timeArray.push(startTime);//push start time to array
    let turns = (endHour - startHour) / hourDecimal;    //to calculate number of time intervals that should be shown
    for (let i = 0; i < turns; i++) {

      var timeTokens = startTime.split(':');
      let newDate = new Date(defaultDate.getFullYear(), defaultDate.getMonth(), defaultDate.getDate(), timeTokens[0], timeTokens[1]);//date format

      let nextDate = new Date(newDate.getTime() + (1000 * 60 * increment));//get incremented date time
      let hours = nextDate.getHours();
      startTime = hours + ":" + nextDate.getMinutes();//extract time part
      startTime = startTime.replace(":0", ":00");
      timeArray.push(startTime);//push time to array
    }

    //to append am and pm to the time slots

    for (let i = 0; i < timeArray.length; i++) {
      var timeSplits = timeArray[i].split(':');
      if (timeSplits[0] <= 11) {
        timeArray[i] = timeArray[i] + " AM";
      }
      else if ((timeSplits[0] >= 12) && (timeSplits[0] <= 23)) {
        timeArray[i] = timeArray[i] + " PM";
      }
    }
    return timeArray;
  }
}



