import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { Constant } from '../../constants/constants';
import { DeviceServiceProvider } from '../device-service/device-service';
import { ReservationServiceProvider } from '../reservation-service/reservation-service';

/*
  Generated class for the HomeServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class HomeServiceProvider {

  constructor(public http: HttpClient, public communication: CommunicationServiceProvider, public constant: Constant, private deviceProvider: DeviceServiceProvider, private reservationProvider: ReservationServiceProvider) {
    console.log('Hello HomeServiceProvider Provider');
  }

  public checkCheckInCheckOutAvailability() {
    return Observable.create(observer => {
      let service = 'AVAILABILITY_CHECK_IN_OUT'; // service type
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('reservaId', this.reservationProvider.objReservation.reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      this.communication.get(url, Params, observer, service); // http get service call

    });

  }

  public checkRoomServiceAvailability() {
    return Observable.create(observer => {
      let service = 'FICHA_RESERVA'; // service type
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('reservaId', this.reservationProvider.objReservation.reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      this.communication.get(url, Params, observer, service); // http get service call
    });
  }

  public alreadyCheckIn() {
    return Observable.create(observer => {
      let service = 'CHECKIN'; // service type
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('reservaId', this.reservationProvider.objReservation.reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      this.communication.get(url, Params, observer, service); // http get service call
    });
  }
}



