import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { Observable } from 'rxjs';
import { Constant } from '../../constants/constants';
import { ReservationServiceProvider } from '../reservation-service/reservation-service';
import { DeviceServiceProvider } from '../device-service/device-service';
import { CheckOutData } from '../../model/checkout-model';
import { CheckinServiceProvider } from '../checkin-service/checkin-service';


@Injectable()
export class CheckoutServiceProvider {

  CheckoutObj: any = [];
  objCheckOut: CheckOutData;

  constructor(public http: HttpClient, public communication: CommunicationServiceProvider,
    public constant: Constant, public reservation: ReservationServiceProvider,
    public deviceProvider: DeviceServiceProvider, public checkinProvider: CheckinServiceProvider,
  ) {
    console.log('Hello CheckoutServiceProvider Provider');

    this.objCheckOut = new CheckOutData();
    this.CheckoutObj = {
      codigoSeguridadTarjetaCredito: null,
      fechaCaducidadTarjetaCredito: null,
      numeroTarjetaCredito: null,
      principal: {},
      tipoTarjetaCredito: null,
      titularTarjeta: null,
      tokenTarjeta: null

    };
  }

  public sendCheckOutData() {
    return Observable.create(observer => {
      if (this.objCheckOut != null) {
        //this.CheckoutObj.id = null;
        //this.CheckoutObj.reservaId = this.reservation.objReservation.reservationId;
        this.CheckoutObj.tipoTarjetaCredito = this.objCheckOut.cardType;
        this.CheckoutObj.numeroTarjetaCredito = this.objCheckOut.cardNumber;
        this.CheckoutObj.codigoSeguridadTarjetaCredito = this.objCheckOut.cvv;
        this.CheckoutObj.fechaCaducidadTarjetaCredito = this.objCheckOut.cardExpirationDate;
        this.CheckoutObj.titularTarjeta = this.objCheckOut.cardHolder;
        this.CheckoutObj.tokenTarjeta = this.objCheckOut.Token;
        // this.CheckoutObj.agreeCheckboxField = true;

        let itemArray: any = {};
        //itemArray.reservaId = this.reservation.objReservation.reservationId;
        //itemArray.id = null;
        itemArray.nombre = this.objCheckOut.guestData.nombre;
        itemArray.apellidos = this.objCheckOut.guestData.apellidos;
        itemArray.fechaNacimiento = this.objCheckOut.guestData.fechaNacimiento;
        itemArray.sexo = this.objCheckOut.guestData.sexo;
        itemArray.documentoIdentidad = this.objCheckOut.guestData.documentoIdentidad;
        itemArray.tipoDocumentoIdentidad = this.objCheckOut.guestData.tipoDocumentoIdentidad;
        itemArray.fechaDocumentoIdentidad = this.objCheckOut.guestData.fechaDocumentoIdentidad;
        itemArray.nacionalidadDocumentoIdentidad = this.objCheckOut.guestData.nacionalidadDocumentoIdentidad;
        itemArray.mail = this.objCheckOut.guestData.mail;
        itemArray.principal = true;
        // itemArray.datosHuespedId = null;
        // itemArray.ciudadResidencia = null;
        // itemArray.codigoPaisResidencia = null;
        itemArray.residencia = this.objCheckOut.guestData.residencia;
        // itemArray.newsletter = true;
        // itemArray.telefono = null;
        itemArray.pictureFront = this.objCheckOut.guestData.pictureFront;
        itemArray.addressStreet = this.objCheckOut.guestData.addressStreet;
        itemArray.addressPostalCode = this.objCheckOut.guestData.addressPostalCode;
        itemArray.addressCity = this.objCheckOut.guestData.addressCity;
        itemArray.addressCountry = this.objCheckOut.guestData.addressCountry;
        // itemArray.huespedYaRegistrado = false;
        // itemArray.formularioRegistroEnviado = null;
        // itemArray.tipoViajeReservaYaDefinido = false;
        // itemArray.numeroHabitacion = null;
        // itemArray.nombreHuesped = this.reservation.objReservation.guestName;
        // itemArray.nombreHotel = this.reservation.objReservation.hotelName;
        // itemArray.entrada = null;
        // itemArray.noches = this.reservation.objReservation.nights;
        // itemArray.personas = this.reservation.objReservation.noOfPersons;
        // itemArray.tipoHabitacion = this.reservation.objReservation.roomType;
        // itemArray.huespedYaCheckInRealizado = this.reservation.objReservation.alreadyCheckIn;
        // itemArray.huespedYaCheckOutRealizado = false;
        // itemArray.localizadorHotel = this.reservation.objReservation.hotelLocation;
        // itemArray.precio = this.reservation.objReservation.price;
        this.CheckoutObj.principal = itemArray;
      }

      let url = this.constant.URL_TRANSACTION_SERVER + '/online/checkout?uuid=' + this.deviceProvider.uuid + '&reservaId=' + this.reservation.objReservation.reservationId;
      this.communication.post(url, this.CheckoutObj, observer); // http get service call
    });

  }

}



