  ionViewDidLoad() {
    this.getStoredPreEnteredCheckinDetails(this.reservation.objReservation.reservationId)
  }

  getStoredPreEnteredCheckinDetails(reservaId) {
    let preData: any = [];
    this.nativeStorage.getItem('checkinPreEnteredDetails').then(checkinData => {
      if (checkinData != undefined) {
        let data = checkinData.guestData;
        preData = data.filter(x => x.reservationID == reservaId);
        if (preData && preData.length > 0) {
          this.commonProvider.objFormData.checkin.checkinCompleted = preData[0].checkinCompleted;
          this.commonProvider.objFormData.checkin.PageCounter = preData[0].pageCounter;
          this.commonProvider.objFormData.checkin.ActiveIndex = preData[0].formIndex;
          this.commonProvider.objFormData.checkin.arrivalTime = preData[0].arrivalTime;
          this.commonProvider.objFormData.checkin.specialRequest = preData[0].specialRequest;
          this.commonProvider.objFormData.checkin.cardHolder = preData[0].cardHolder;
          this.commonProvider.objFormData.checkin.agreeTerms = preData[0].agreeTerms;
          this.commonProvider.objFormData.checkin.assignCard = preData[0].assignCard;

          this.commonProvider.objFormData.checkin.Name = [];
          this.commonProvider.objFormData.checkin.LastName = [];
          this.commonProvider.objFormData.checkin.Address = [];
          this.commonProvider.objFormData.checkin.ZipCode = [];
          this.commonProvider.objFormData.checkin.City = [];
          this.commonProvider.objFormData.checkin.Country = [];
          this.commonProvider.objFormData.checkin.idNumber = [];
          this.commonProvider.objFormData.checkin.Document = [];
          this.commonProvider.objFormData.checkin.Nationality = [];
          this.commonProvider.objFormData.checkin.Residence = [];
          this.commonProvider.objFormData.checkin.DOB = [];
          this.commonProvider.objFormData.checkin.Gender = [];
          this.commonProvider.objFormData.checkin.DateOfIssue = [];
          this.commonProvider.objFormData.checkin.pictureFront = [];

          let guestList = preData[0].guests;
          for (let q = 0; q < guestList.length; q++) {

            this.commonProvider.objFormData.checkin.Name.push(guestList[q].Name);
            this.commonProvider.objFormData.checkin.LastName.push(guestList[q].LastName);
            this.commonProvider.objFormData.checkin.Address.push(guestList[q].Address);
            this.commonProvider.objFormData.checkin.ZipCode.push(guestList[q].ZipCode);
            this.commonProvider.objFormData.checkin.City.push(guestList[q].City);
            this.commonProvider.objFormData.checkin.Country.push(guestList[q].Country);
            this.commonProvider.objFormData.checkin.idNumber.push(guestList[q].idNumber);
            this.commonProvider.objFormData.checkin.Document.push(guestList[q].Document);
            this.commonProvider.objFormData.checkin.Nationality.push(guestList[q].Nationality);
            this.commonProvider.objFormData.checkin.Residence.push(guestList[q].Residence);
            this.commonProvider.objFormData.checkin.DOB.push(guestList[q].DOB);
            this.commonProvider.objFormData.checkin.Gender.push(guestList[q].Gender);
            this.commonProvider.objFormData.checkin.DateOfIssue.push(guestList[q].DateOfIssue);
            this.commonProvider.objFormData.checkin.pictureFront.push(guestList[q].pictureFront);

          }

          console.log(preData);
        }
      }
    });
  }
