  fnCheckRoomServiceAvailability() {
    this.showLoading();
    if (this.reservation.objReservation.bedrooms) {
      this.prepareRoomServicePage();
      //this.navCtrl.push("RoomServicePage", {}, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
    }
    else {
      this.home.checkRoomServiceAvailability().subscribe(data => {
        if (data.success) {
          var reservationDataResponse = data.response;
          if (reservationDataResponse.numeroHabitacion) {
            this.reservation.objReservation.bedrooms = parseInt(reservationDataResponse.numeroHabitacion);

            //storing roomNumber into native storage
            this.nativeStorage.setItem('reservationData', {
              'roomNumber': this.reservation.objReservation.bedrooms
            })
              .then(
              () => console.log('Stored item!'),
              error => console.error('Error storing item', error)
              );
            this.prepareRoomServicePage();

          }
          else {
            this.loading.dismiss();
            // the check-in has not been completed
            let checkinMsg = this.tools.fnLanguageTranslate(['common.error', "view.servicioHabitacion.error.checkinNoRealizado", "buttons.ok"]);
            checkinMsg.subscribe((value: string) => {
              let alertCheckin = this.alertCtrl.create({
                title: value['common.error'],
                message: value['view.servicioHabitacion.error.checkinNoRealizado'],
                cssClass: 'cls_alrt',
                buttons: [
                  {
                    text: value['buttons.ok'],
                    role: 'cancel',
                    handler: data => {
                    }
                  }]
              });
              alertCheckin.present();
              setTimeout(() => { alertCheckin.dismiss(); }, 5000);
            });
          }
        }
        else {
          this.loading.dismiss();
          let checkinMsg3 = this.tools.fnLanguageTranslate(['common.error', "common.connectionError", "buttons.ok"]);
          checkinMsg3.subscribe((value: string) => {
            this.showAlert(value['common.error'], value['common.connectionError'], 'cls_alrt', value['buttons.ok']);
          });
        }
      });
    }
  }
