import { Component, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, AlertController, Platform } from 'ionic-angular';
import { ToolsProvider } from '../../providers/tools/tools';
import { DomSanitizer } from '@angular/platform-browser';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { AutoCompleteCountryPage } from '../auto-complete-country/auto-complete-country';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { DatePipe } from '@angular/common';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { HomePage } from '../home/home';
import { GuestData } from '../../model/guest-data-model';
import { GuestServiceProvider } from '../../providers/guest-service/guest-service';
import { RegistrationServiceProvider } from '../../providers/registration-service/registration-service';
import { NativeStorage } from '@ionic-native/native-storage';
import { Keyboard } from '@ionic-native/keyboard';

@IonicPage()
@Component({
  selector: 'page-registration',
  templateUrl: 'registration.html',
  providers: [Keyboard]
})
export class RegistrationPage {
  registrationForm: FormGroup; // registration form
  isChecked = false; // to manage radio button
  isCheckedPrivacy = false;
  isCheckedConditions = false;
  htmlData: any = null; // to manage html content
  res: any; //to store return data while language translate
  res2: any;
  countryCode: string; //to store country code
  backgroundImageStyle: any; // to store background image url
  maxDate: any;
  countryName: any;
  monthNames: any; // for month names
  monthShortNames: any = []; // for month short names
  selectedCountry: string;
  minDate: any;
  devicePlatForm: any = "android";
  privacyUrl: any;
  legalUrl: any;
  htmlDataPrivacy: any;

  constructor(private keyboard: Keyboard, public navCtrl: NavController, public navParams: NavParams, public tools: ToolsProvider, private modalCtrl: ModalController, public cdRef: ChangeDetectorRef, public formBuilder: FormBuilder, public sanitized: DomSanitizer, public alertCtrl: AlertController, public reservation: ReservationServiceProvider, private datePipe: DatePipe, public hotel: HotelServiceProvider, public guestPvdr: GuestServiceProvider, private registrationPvdr: RegistrationServiceProvider, public nativeStorage: NativeStorage, private platForm: Platform, public iab: InAppBrowser) {
    this.registrationForm = formBuilder.group({
      'name': new FormControl('', [Validators.required]),
      'lastName': new FormControl('', [Validators.required]),
      'country': new FormControl('', [Validators.required]),
      'city': new FormControl(''),
      'dateOfBirth': new FormControl('', [Validators.required]),
      'gender': new FormControl('', [Validators.required]),
      'email': new FormControl('', [Validators.required, Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)]),
      'typeOfTravel': new FormControl('', [Validators.required])

    });
    // translate month names
    let monthList = this.tools.fnLanguageTranslate("common.monthNames");
    monthList.subscribe((value: string) => {
      this.monthNames = value;
    });
    //translated month short names
    for (let i = 0; i < 12; i++) {
      this.monthShortNames.push(this.tools.getShortMonthName(i));
    }
    if (this.platForm.is("ios")) {
      this.devicePlatForm = "ios";
    }
  }

  ionViewDidLoad() {
    let current = this;
    this.maxDate = this.tools.fnGetToday();
    this.minDate = (new Date()).getFullYear() - 90;
    let dt = this.reservation.objReservation.createdDate; // gets created date from reservation object
    let rmTime = dt.split(" ");
    let newDate = rmTime[0].split("-").reverse().join("-");
    let date = this.datePipe.transform(newDate, 'dd/MMM/yyyy'); // use datepipe to transform the date to required format

    this.res = this.tools.fnLanguageTranslateWithParam("view.checkin.template.card", { localizadorHotel: this.reservation.objReservation.hotelLocation, fechaEntrada: date, noches: this.reservation.objReservation.nights, tipoHabitacion: this.reservation.objReservation.roomType, habitaciones: "this.reservation.objReservation.bedrooms", personas: this.reservation.objReservation.noOfPersons, precio: this.reservation.objReservation.price, moneda: this.reservation.objReservation.price }); // translate of html content with common funtion
    this.res.subscribe((value: string) => {
      this.htmlData = this.sanitized.bypassSecurityTrustHtml(value); // bypass html content to avoid losing html attributes
      this.cdRef.detectChanges();
      document.getElementById('habitaciones').style.display = 'none';
      document.getElementById('precio').style.display = 'none';

      /* click binding to termsofservice span element and redirection */
      let termsElmnt = document.querySelector('ion-nav').querySelectorAll("#termsOfService");
      for (let i = 0; i < termsElmnt.length; i++) {
        if (termsElmnt[i]) {
          termsElmnt[i].addEventListener('click', function (e) {
            current.iab.create(current.tools.getLegalUrl(), "_blank", "location=yes,toolbar=yes,hardwareback=yes");
          });
        }
      }
    });

    this.res2 = this.tools.fnLanguageTranslate("view.login.label.privacy");
    this.res2.subscribe((value: string) => {
      this.htmlDataPrivacy = this.sanitized.bypassSecurityTrustHtml(value);
      this.cdRef.detectChanges();

      /* click binding to termsofservice span element and redirection */
      let termsElmnt1 = document.querySelector('ion-nav').querySelectorAll("#privacyPolicy");
      for (let i = 0; i < termsElmnt1.length; i++) {
        if (termsElmnt1[i]) {
          termsElmnt1[i].addEventListener('click', function (e) {
            current.iab.create(current.tools.getPrivacyUrl(), "_blank", "location=yes,toolbar=yes,hardwareback=yes");
          });
        }
      }
      /* click binding to termsofservice span element and redirection */
      let termsElmnt = document.querySelector('ion-nav').querySelectorAll("#termsOfService");
      for (let i = 0; i < termsElmnt.length; i++) {
        if (termsElmnt[i]) {
          termsElmnt[i].addEventListener('click', function (e) {
            current.iab.create(current.tools.getLegalUrl(), "_blank", "location=yes,toolbar=yes,hardwareback=yes");
          });
        }
      }

    });

    this.backgroundImageStyle = this.tools.getImagePath(this.hotel.objHotel.mainImage); // returns image url
    this.registrationForm.get('name').setValue(this.reservation.objReservation.guestName); // set name to registration page on load
    this.registrationForm.get('lastName').setValue(this.reservation.objReservation.lastName); // set last name to registration page on load
    this.registrationForm.get('email').setValue(this.reservation.objReservation.email); // set email to registration page on load

    this.legalUrl = this.tools.getLegalUrl();
    this.privacyUrl = this.tools.getPrivacyUrl();
  }

  /**
   * function to select or deselect the radio button
   * @param  event
  */
  public fnOnRadioSelect($event) {
    this.isChecked = !this.isChecked;
  }

  public fnOnRadioSelectPrivacy($event) {
    this.isCheckedPrivacy = !this.isCheckedPrivacy;
  }

  public fnOnRadioSelectConditions($event) {
    this.isCheckedConditions = !this.isCheckedConditions;
  }

  /**
   * Function to show country list
   */
  public showCountryList() {
    let modal = this.modalCtrl.create(AutoCompleteCountryPage, { selectedItem: this.selectedCountry });
    modal.onDidDismiss(data => {
      if (data != null) {
        this.registrationForm.get('country').setValue(data.text);
        this.countryCode = data.value;
        this.countryName = data.text;
        this.selectedCountry = data.value;
      }
    });

    modal.present();
  }


  /**
   * function to valdate and execute after submitting the form
   */
  public fnSubmit() {

    if (!this.registrationForm.valid) {
      let emptyFields = []; // to add empty fields in the form
      let alertMessage: any; // to show alert message on validation
      let validateMessages = this.tools.fnLanguageTranslate(['view.checkin.label.nombre.title', 'view.checkin.label.apellidos.title', 'view.checkin.label.mail.title', 'view.registration.label.country', 'view.checkin.label.fechaNacimiento.title', 'view.checkin.label.sexo.title', 'view.registration.label.travelType']);
      if (this.registrationForm.controls['name'].hasError('required')) { // checks name field is empty
        validateMessages.subscribe((value: string) => {
          emptyFields.push(value['view.checkin.label.nombre.title']);
        });
      }
      if (this.registrationForm.controls['lastName'].hasError('required')) { // checks surName field is empty
        validateMessages.subscribe((value: string) => {
          emptyFields.push(value['view.checkin.label.apellidos.title']);
        });
      }
      if (this.registrationForm.controls['email'].hasError('required')) { // checks surName field is empty
        validateMessages.subscribe((value: string) => {
          emptyFields.push(value['view.checkin.label.mail.title']);
        });
      }
      if (this.registrationForm.controls['country'].hasError('required')) { // checks email field is empty
        validateMessages.subscribe((value: string) => {
          emptyFields.push(value['view.registration.label.country']);
        });
      }
      if (this.registrationForm.controls['dateOfBirth'].hasError('required')) { // checks surName field is empty
        validateMessages.subscribe((value: string) => {
          emptyFields.push(value['view.checkin.label.fechaNacimiento.title']);
        });
      }
      if (this.registrationForm.controls['gender'].hasError('required')) { // checks email field is empty
        validateMessages.subscribe((value: string) => {
          emptyFields.push(value['view.checkin.label.sexo.title']);
        });
      }
      if (this.registrationForm.controls['typeOfTravel'].hasError('required')) { // checks email field is empty
        validateMessages.subscribe((value: string) => {
          emptyFields.push(value['view.registration.label.travelType']);
        });
      }

      // for language translation
      let msg = this.tools.fnLanguageTranslateWithParam(["common.mandatoryFields", "view.login.error.emptyMail", "common.error", "buttons.ok"], { fields: emptyFields }); // translate of html content with common funtion

      if (this.registrationForm.controls['name'].hasError('required')
        || this.registrationForm.controls['lastName'].hasError('required')
        || this.registrationForm.controls['email'].hasError('required')
        || this.registrationForm.controls['country'].hasError('required')
        || this.registrationForm.controls['dateOfBirth'].hasError('required')
        || this.registrationForm.controls['gender'].hasError('required')
        || this.registrationForm.controls['typeOfTravel'].hasError('required')) {
        msg.subscribe((value: string) => {
          alertMessage = value['common.mandatoryFields'];
        });
      } else {
        msg.subscribe((value: string) => {
          alertMessage = value['view.login.error.emptyMail'];
        });
      }

      msg.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: alertMessage,
          cssClass: 'cls_alrt cls_reg_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);

      });
    } else {

      if (!this.isCheckedPrivacy) {
        // for language translation
        let msg = this.tools.fnLanguageTranslate(["common.error", "buttons.ok", "view.login.label.privacyerrormessage"]); // translate of html content with common funtion
        msg.subscribe((value: string) => {
          let emptyFields = [];
          let alertnew = this.alertCtrl.create({
            title: value['common.error'],
            message: value['view.login.label.privacyerrormessage'],
            cssClass: 'cls_alrt cls_reg_alrt',
            buttons: [
              {
                text: value['buttons.ok'],
                role: 'cancel'
              }]
          });
          alertnew.present();
          setTimeout(() => { alertnew.dismiss(); }, 5000);
        })
      }else{
        let dt = this.registrationForm.get('dateOfBirth').value;
        let dob = this.datePipe.transform(dt, 'dd-MM-yyyy HH:mm:ss'); // transform the dateOfBirth value from form to required format
        let name = this.registrationForm.get('name').value;
        let lastName = this.registrationForm.get('lastName').value;
        let email = this.registrationForm.get('email').value;
        let country = this.countryCode;
        let city = this.registrationForm.get('city').value;
        let dateOfBirth = dob; //this.registrationForm.get('dateOfBirth').value;
        let gender = this.registrationForm.get('gender').value;
        let typeOfTravel = this.registrationForm.get('typeOfTravel').value;
        let newsLetter = this.isChecked;
        this.registrationPvdr.sendFullRegistration(name, lastName, email, country, dateOfBirth, gender, typeOfTravel, newsLetter, city).subscribe(data => {
          if (data.success == true) {
            this.guestPvdr.objGuest = new GuestData();
            this.reservation.objReservation.alreadyRegistered = true;
            this.reservation.objReservation.registrationCompleted = true;

            this.guestPvdr.objGuest.name = name;
            this.guestPvdr.objGuest.lastName = lastName;
            this.guestPvdr.objGuest.email = email;
            this.guestPvdr.objGuest.gender = gender;
            this.guestPvdr.objGuest.newsLetter = newsLetter;
            this.guestPvdr.objGuest.cityResidence = city;
            this.guestPvdr.objGuest.countryCodeResidence = country;
            this.guestPvdr.objGuest.dateOfBirth = dateOfBirth;
            this.guestPvdr.objGuest.addressCountry = this.countryName;
            // set local storage data
            this.nativeStorage.getItem('reservationData').then(reservation => {
              if (reservation != undefined) {
                this.nativeStorage.setItem('reservationData', {
                  'localizer': reservation.localizer,
                  'reservationId': reservation.reservationId,
                  'name': reservation.name,
                  'lastname': reservation.lastname,
                  'email': reservation.email,
                  'registrationCompleted': true,
                  'alreadyRegistered': true,
                  'noStayGuest': reservation.noStayGuest,
                  'response': reservation.response
                })
                  .then(
                    () => console.log('Stored item!'),
                    error => console.error('Error storing item', error)
                  );

              }
            });

            this.nativeStorage.setItem('reservationData', {
              'registrationCompleted': true, 'alreadyRegistered': true
            })

            this.navCtrl.setRoot(HomePage, {}, { animate: true, animation: 'transition', duration: 1000, direction: 'forward' }); // on successful registration navigate to home page

          } else {

            let errmsg = this.tools.fnLanguageTranslate(["common.error", "common.serviceIsUnavailable", "buttons.ok"]); // translate of error message

            errmsg.subscribe((value: string) => {  // when registration fails show alert
              let alertnew = this.alertCtrl.create({
                title: value['common.error'],
                message: value['common.serviceIsUnavailable'],
                cssClass: 'cls_alrt',
                buttons: [
                  {
                    text: value["buttons.ok"],
                    role: 'cancel'
                  }]
              });
              alertnew.present();
              setTimeout(() => { alertnew.dismiss(); }, 5000);

            });
          }

        });
      }


    }
  }
}



