    PUSH_KEY = "3ae88d10-d238-4705-963d-7c56fd3861d1";

    _URL_INFO_SERVER = this.get('INFO_SERVER_PROTOCOL') + "://" + this.get('INFO_SERVER') + ":" + this.get('INFO_SERVER_PORT') + "/info-server/" + this.get('_API_VERSION') + "/info";

    _URL_TRANSACTION_SERVER = this.get('TRANSACTION_SERVER_PROTOCOL') + "://" + this.get('TRANSACTION_SERVER') + ":" + this.get('TRANSACTION_SERVER_PORT') + "/transaction-services/" + this.get('_API_VERSION') + "/ts";

    _URL_NOTIFICATION_SERVER = this.get('NOTIFICATION_SERVER_PROTOCOL') + "://" + this.get('NOTIFICATION_SERVER') + ":" + this.get('NOTIFICATION_SERVER_PORT') + "/notification-services/" + this.get('_API_VERSION') + "/notification";


