import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Constant } from '../../constants/constants';
import { HotelServiceProvider } from '../hotel-service/hotel-service';
import { ReservationServiceProvider } from '../reservation-service/reservation-service';
import { DeviceServiceProvider } from '../device-service/device-service';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { RoomService } from '../../model/room-service-model';

/*
  Generated class for the RoomsServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class RoomsServiceProvider {

  objRoomService: RoomService;
  constructor(public http: HttpClient, public hotelProvider: HotelServiceProvider,
    public reservationProvider: ReservationServiceProvider, public constant: Constant,
    private deviceProvider: DeviceServiceProvider,
    public communicationProvider: CommunicationServiceProvider
  ) {
    console.log('Hello RoomsServiceProvider Provider');
  }
  //to set all Room Services data
  public getRoomServiceInfo() {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/cacheable/hotel/' + this.hotelProvider.objHotel.id + '/servicios-habitacion';
      let Params = new HttpParams();
      Params = Params.append('reservaId', this.reservationProvider.objReservation.reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      this.communicationProvider.get(url, Params, observer); // http get service call
    });
  }

  //to set all items under a room service
  public getServiceItems() {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/cacheable/hotel/' + this.hotelProvider.objHotel.id + '/servicios-habitacion/items';
      let Params = new HttpParams();
      Params = Params.append('reservaId', this.reservationProvider.objReservation.reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      this.communicationProvider.get(url, Params, observer); // http get service call
    });
  }

  public fnGetTimeSlotsForRoomService(today?) {

    let startTime = "00:00",
      endTime = "23:45",
      increment: any = 15;

    let hourDecimal = parseInt(increment) / 60;
    let turns: any = '';
    let defaultDate: Date = new Date();
    let nextDate = new Date();
    let newDate: any;
    let timeSlots: any = [];
    let currentHour: any = defaultDate.getHours() == 0 ? 24 : defaultDate.getHours();
    let startReminder = defaultDate.getMinutes() % increment;
    let startMinute = (defaultDate.getMinutes() - startReminder) + increment;
    if (today == true) {
      startTime = currentHour + ':' + startMinute;
      let startTimeTokens: any = startTime.split(':');
      newDate = new Date(defaultDate.getFullYear(), defaultDate.getMonth(), defaultDate.getDate(), startTimeTokens[0], startTimeTokens[1]);//date format

      nextDate = new Date(newDate.getTime() + (1000 * 60 * 30));//get incremented date time
      startTime = nextDate.getHours() + ":" + nextDate.getMinutes();//extract time part
      startTime = startTime.replace(":0", ":00");
    }
    
    timeSlots.push(startTime);//push start time to array

    // end time to timedata
    let endTimeTokens: any = endTime.split(':');
    let endDate = new Date(defaultDate.getFullYear(), defaultDate.getMonth(), defaultDate.getDate(), endTimeTokens[0], endTimeTokens[1]);//date format
    let endHour = endDate.getHours() == 0 ? 24 : endDate.getHours();

    // start time to timedata
    let startTimeTokens: any = startTime.split(':');
    let startDate = new Date(defaultDate.getFullYear(), defaultDate.getMonth(), defaultDate.getDate(), startTimeTokens[0], startTimeTokens[1]);//date format
    let startHour = startDate.getHours();

    turns = (endHour - startHour) / hourDecimal;    //to calculate number of time intervals that should be shown
    for (let i = 0; i < turns; i++) {

      var timeTokens: any = startTime.split(':');
      newDate = new Date(defaultDate.getFullYear(), defaultDate.getMonth(), defaultDate.getDate(), timeTokens[0], timeTokens[1]);//date format

      nextDate = new Date(newDate.getTime() + (1000 * 60 * increment));//get incremented date time
      startTime = nextDate.getHours() + ":" + nextDate.getMinutes();//extract time part
      startTime = startTime.replace(":0", ":00");
      //change start time is selected date is today

      timeSlots.push(startTime);//push time to array

    }
    return timeSlots; // returns time slots as array
  }

  //service call to request check-in
  public sendRoomServiceRequest(date, comments) {

    return Observable.create(observer => {
      let itemObj: any = [];
      if (this.objRoomService != null) {
        let itemList = this.objRoomService.serviceItemsList;
        for (var i = 0; i < itemList.length; i++) {
          if (itemList[i].quantity > 0) {
            let itemArray: any = {};
            itemArray.cantidad = itemList[i].quantity;
            itemArray.precio = itemList[i].price;
            itemArray.id = itemList[i].id;
            itemObj.push(itemArray);
          }

        }

      }
      let serviceData: any = {
        'items': itemObj
      };

      let service = 'CREATE_ROOMSERVICE_RESERVATION';
      let qryParams = 'fechaHora=' + date + '&comentario=' + comments + '&uuid=' + this.deviceProvider.uuid + '&reservaId=' + this.reservationProvider.objReservation.reservationId;
      let url = this.constant.URL_TRANSACTION_SERVER;

      this.communicationProvider.post(url, serviceData, observer, service, qryParams); // http post service call
    });
  }

}



