import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Restaurant } from '../../model/restaurant-model';
import { Observable } from 'rxjs';
import { Constant } from '../../constants/constants';
import { DeviceServiceProvider } from '../device-service/device-service';
import { CommunicationServiceProvider } from '../communication-service/communication-service';
import { ReservationServiceProvider } from '../reservation-service/reservation-service';
import { HotelServiceProvider } from '../hotel-service/hotel-service';
/*
  Generated class for the RestaurantServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class RestaurantServiceProvider {

  objRestaurant: Restaurant;

  constructor(public http: HttpClient, public constant: Constant, private deviceProvider: DeviceServiceProvider,
    public communicationProvider: CommunicationServiceProvider, public reservationProvider: ReservationServiceProvider,
    public hotelProvider: HotelServiceProvider) {
    console.log('Hello RestaurantServiceProvider Provider');
  }

  //to set all restaurant data
  public getRestaurantInfo() {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/cacheable/hotel/' + this.hotelProvider.objHotel.id + '/restaurantes';
      let Params = new HttpParams();
      Params = Params.append('reservaId', this.reservationProvider.objReservation.reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      this.communicationProvider.get(url, Params, observer); // http get service call
    });

  }

  public getDishesInfo() {
    return Observable.create(observer => {
      let url = this.constant.URL_TRANSACTION_SERVER + '/cacheable/hotel/' + this.hotelProvider.objHotel.id + '/restaurantes/platos';
      let Params = new HttpParams();
      Params = Params.append('reservaId', this.reservationProvider.objReservation.reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid); // parameters
      this.communicationProvider.get(url, Params, observer); // http get service call
    });

  }

  public sendServiceReservationWithPayment(reservationId, serviceId, date, turn, noOfPersons, comments, cardholder, token) {
    return Observable.create(observer => {
      let service = 'CREATE_RESTAURANT_RESERVATION_PAYMENT';
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('serviceId', serviceId);
      Params = Params.append('fecha', date);
      Params = Params.append('turno', turn);
      Params = Params.append('pax', noOfPersons);
      Params = Params.append('comentario', comments);
      Params = Params.append('titularTarjeta', cardholder);
      Params = Params.append('tokenTarjeta', token);
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer, service);

    });

  }

  public sendRestaurantReservation(serviceId, date, turn, person, comments, reservationId) {
    return Observable.create(observer => {
      let service = 'CREATE_RESTAURANT_RESERVATION'; // service type
      let url = this.constant.URL_TRANSACTION_SERVER;
      let Params = new HttpParams();
      Params = Params.append('serviceId', serviceId); // params
      Params = Params.append('fecha', date);
      Params = Params.append('turno', turn);
      Params = Params.append('pax', person);
      Params = Params.append('comentario', comments);
      Params = Params.append('reservaId', reservationId);
      Params = Params.append('uuid', this.deviceProvider.uuid);
      this.communicationProvider.post(url, Params, observer, service); // http get service call

    });
  }

}



