import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Loading, Platform, AlertController } from 'ionic-angular';
import { MenuController } from 'ionic-angular';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { ToolsProvider } from '../../providers/tools/tools';
import { CommonServiceProvider } from '../../providers/common-service/common-service';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { NotificationProvider } from '../../providers/notification/notification';
/**
 * Generated class for the HotelInfoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-hotel-info',
  templateUrl: 'hotel-info.html',
})
export class HotelInfoPage {
  sendMessageForm: FormGroup;
  loading: Loading;
  backgroundImageUrl: string = "";
  nombre: string = "";
  id: number;
  telefono: string = "";
  direccion: string = "";
  poblacion: string = "";
  codigoPostal: string = "";
  provincia: string = "";
  paisNombre: string = "";
  lat: string = "";
  lon: string = "";
  textFocused = false;

  constructor(public alertCtrl: AlertController, private platform: Platform, public formBuilder: FormBuilder, public navCtrl: NavController, public navParams: NavParams, private hotelProvider: HotelServiceProvider, private toolsProvider: ToolsProvider, public menuCtrl: MenuController, private commonProvider: CommonServiceProvider, public loadingCtrl: LoadingController, private notificationProvider: NotificationProvider) {
    this.menuCtrl.close();
    this.backgroundImageUrl = this.toolsProvider.getImagePath(hotelProvider.objHotel.imageInformation);

    this.nombre = this.hotelProvider.objHotel.hotelName;
    this.id = this.hotelProvider.objHotel.id;
    this.telefono = this.hotelProvider.objHotel.telephone;
    this.direccion = this.hotelProvider.objHotel.address;
    this.poblacion = this.hotelProvider.objHotel.location;
    this.codigoPostal = this.hotelProvider.objHotel.postalCode;
    this.provincia = this.hotelProvider.objHotel.province;
    this.paisNombre = this.hotelProvider.objHotel.country;
    this.lat = this.hotelProvider.objHotel.lat;
    this.lon = this.hotelProvider.objHotel.lon;

    this.sendMessageForm = formBuilder.group({
      'commentsTextAreaField': new FormControl('', [Validators.required])
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HotelInfoPage');
  }
  gotoCall() {
    if (this.telefono != null && this.telefono != undefined) {

      this.platform.ready().then(() => {
        if (this.platform.is('ios')) {
          window.open('tel:' + this.telefono);
        } else {
          //android and other platforms
          window.open('tel://' + this.telefono);
        }

      });

    } else {
      console.log("no phone number");
    }
  }
  gotoMap() {

    if (this.lat != null && this.lat != undefined && this.lon != null && this.lon != undefined) {

      let hotelName = this.nombre.trim().replace(/\s+/g, "+");
      this.platform.ready().then(() => {
        // ios
        if (this.platform.is('ios')) {
          window.open("maps://?q=" + hotelName + "&ll=" + this.lat + "," + this.lon, '_system');
        } else if (this.platform.is('android')) {
          //android and other platforms
          window.open('geo://' + this.lat + ',' + this.lon + '?q=' + this.lat + ',' + this.lon + '(' + hotelName + ')', '_system');
        } else {
          window.open('https://maps.google.com/maps?daddr=' + this.lat + ',' + this.lon + '&amp;ll="');
        }
      });
    } else {
      console.log("no lat or lon");
    }
  }

  //Back button
  public fnBack() {
    this.navCtrl.popToRoot({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }
  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }
  fnSend() {
    if (this.sendMessageForm.valid) {
      this.showLoading();
      this.commonProvider.sendMessage(this.sendMessageForm.value["commentsTextAreaField"]).subscribe(data => {
        this.loading.dismiss();
        if (data.success == true) {
          let checkoutMsg = this.toolsProvider.fnLanguageTranslate(['common.requestReceived', "view.hotelInfo.successMsg", "buttons.ok"]);
          checkoutMsg.subscribe((value: string) => {
            this.showAlert(value['common.requestReceived'], value['view.hotelInfo.successMsg'], 'cls_alrt', value['buttons.ok']);
            this.sendMessageForm.setValue({ commentsTextAreaField: "" });
          });

        } else {
          let checkoutMsg = this.toolsProvider.fnLanguageTranslate(['common.error', "view.reservation.error.noPossible", "buttons.ok"]);
          checkoutMsg.subscribe((value: string) => {
            this.showAlert(value['common.error'], value['view.reservation.error.noPossible'], 'cls_alrt', value['buttons.ok']);
          });
        }
      });
    } else {
      let checkoutMsg = this.toolsProvider.fnLanguageTranslate(['common.error', "view.hotelInfo.emptyMsg", "buttons.ok"]);
      checkoutMsg.subscribe((value: string) => {
        this.showAlert(value['common.error'], value['view.hotelInfo.emptyMsg'], 'cls_alrt', value['buttons.ok']);
      });

    }
  }
  showAlert(title, message, css, buttonText) {
    let alertCheckin = this.alertCtrl.create({
      title: title,
      message: message,
      cssClass: css,
      buttons: [
        {
          text: buttonText,
          role: 'cancel',
          handler: data => {
          }
        }]
    });
    alertCheckin.present();
    setTimeout(() => { alertCheckin.dismiss(); }, 5000);
  }

  textFocus(){
    this.textFocused = true;
  }

  textBlur(){
    this.textFocused = false;
  }

}



